import React from 'react';
import {Button,Modal, ModalHeader, ModalBody, ModalFooter, Row, ListGroup, ListGroupItem, Input} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import '../../styles/css/style.css'
// import ListGroup from './ListGroups';

export default class ModalListAdmin extends React.Component{
    constructor(props){
        super(props);
        let listAdmin = this.props.listAdmin || [];
        this.state={
            modal: false,
            listAdmin: listAdmin,
            search:''
        }
        this.toggle = this.toggle.bind(this);

    }
    toggle(){
        this.setState(prevState => ({
            modal: !prevState.modal
        }))
    }

    selectAdmin(index, data){
        if(this.props.onChange){
            this.props.onChange(index,data)
        }
    }

    searchInput( value){
        value = value.toUpperCase();
        let listAdmin = this.state.listAdmin;
        let ul = document.getElementById("listAdmin");
        let listLi = ul.getElementsByTagName("li");
        for(let i = 0; i < listAdmin.length; i++){
            let adminName = listAdmin[i].name.toUpperCase();
            if(adminName.indexOf(value)>-1){
                listLi[i].style.display = "flex";
            }
            else{
                listLi[i].style.display="none";
            }
        }
    }

    render(){
        let listAdmin = this.state.listAdmin;
        let adminsRender = [];
        for(let i = 0; i < listAdmin.length; i++){
            adminsRender.push(
                <ListGroupItem key={i} style={{"display":"flex"}}>
                    <div style={{"width":"33.33%"}} className="px-2">{listAdmin[i].name}</div>
                    <div style={{"width":"33.33%"}} className="px-2">{listAdmin[i].email}</div>
                    <div style={{"width":"33.33%"}} className="px-2 modal-content">
                        <Button color="primary" onClick={() =>this.selectAdmin(i,listAdmin[i])}>Thêm vào nhóm</Button>
                    </div>
                </ListGroupItem>
            )
        }
        
        return(
            <div style={{"width":"100%"}}>
                <Button color="success" onClick={this.toggle}> 
                    <FontAwesomeIcon icon={this.props.icon}/> 
                    {this.props.buttonLabel}
                </Button>

                <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
                <ModalHeader toggle={this.toggle}>Thêm thành viên</ModalHeader>
                    <Input
                        style={{"width":"50%", "margin":"auto"}}
                        placeholder={"Tìm kiếm"}
                        name="search-input"
                        onChange={(event)=>this.searchInput(event.target.value)}
                    />
                <ModalBody>
                   <ListGroup id={"listAdmin"}>
                       {adminsRender}
                   </ListGroup>
                </ModalBody>
                <ModalFooter>
                    {/* <Button color="primary" onClick={this.toggle}>Do Something</Button>{' '} */}
                    <Button color="secondary" onClick={this.toggle}>Đóng</Button>
                </ModalFooter>
                </Modal>
            </div>
        )
    }
}