import Page from '../../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Input } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import Api from '../../utils/api';
import Utils from '../../utils/utils';
import { Link } from 'react-router-dom';
import Moment from '../../components/Moment';
import Config from '../../utils/config';
import ModalHandle from '../../components/ModalHandle';
import Video from './Video';
import UserDetail from '../../Users/UserDetailModal';
import DropDown from '../../components/DropDown';
import Toast from '../../components/Toast';


const statusObject = {
  approved:{variant:'success', position:0}, 
  rejected:{variant:'secondary', position:1},
  pending: {variant:'warning', position:2}
}
const videoStatus = [
  {name:'Approved', _id:'approved'},
  {name:'Rejected', _id:'rejected'},
  {name:'Pending', _id:'pending'}
]

class TablePost extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      error: null,
      pages: null,
      page: 0,
      pageSize: 10,
      columns: this.defaultColumns,
      listCategories: [],
      status: '',
      statusVideo: '',
      categories: '',
      modalShow: false,
      modalComponent: '',
      modalId: '',
    };
  }

  componentDidMount() {
    Api.getCategory('active').then(res => {
      if (res.success) {
        const listCategories = res.data.map(el => ({
          value: el._id,
          label: el.name
        }));
        this.setState({
          listCategories
        });
      }
    });
  }

  changeStatus(index, status, video_id, videoIndex) {
    
    Api.changeVideoStatus({ video_id, status }).then(response => {
      if (response.success) {
        // this.setState({ data: this.state.data.filter(video => video._id !== item._id) });
        let data = this.state.data;
        data[videoIndex].status = status;
        this.setState({data});
        this.toast.showMessage('Thay đổi thành công')
      } else {
        this.toast.showMessage('Thay đổi thất bại');
      }
    });
  }

  getData = (state, instance) => {
    let cols = this.defaultColumns;
    if (state.status ? state.status !== 'approved' : this.state.status !== 'approved') {
      cols = this.defaultColumns.filter(item => item.accessor !== 'statistics');
    }
    const sort = state.sorted && state.sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString();
    this.setState({
      isLoading: true,
      data: [],
      pages: state.pages,
      page: state.page,
      pageSize: state.pageSize,
      columns: cols
    });
    Api.getVideos({
      categories: state.categories || this.state.categories,
      status: state.status || this.state.status,
      page: state.page + 1,
      limit: state.pageSize,
      sort
    }).then(response => {
      if (response.success) {
        const data = response.data.map(item => {
          let listCategories = '';
          for (let i = 0; i < item.categories.length; i++) {
            if (i != 0) {
              listCategories += ', ';
            }
            listCategories += item.categories[i].category_name;
          }
          item.categoryNames = listCategories;
          item.thumbnail = Utils.getYoutubeThumbnail(item.youtube_id);
          return item;
        });

        this.setState({
          isLoading: false,
          pages: response.total_page,
          data
        });
      } else {
        this.setState({
          isLoading: false,
          error: 'Tải dữ liệu lỗi.'
        });
      }
    });
  };

  onReload = () => {
    this.getData(this.state);
  };

  onChangeModal(modalId, modalComponent){
    let modalShow = true;
    this.setState({modalId, modalShow, modalComponent});
  }
  onCloseModal(){
    this.setState({modalShow: false});
  }

  onChange = e =>{
    this.setState({ [e.target.name]: e.target.value });
    let state = this.state;
    if(e.target.value === '')
      state[e.target.name] = '';
      this.getData({ ...state, [e.target.name]: e.target.value });
  };

  defaultColumns = [
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Ảnh
        </span>
      ),
      accessor: d => [d.link, d.thumbnail, d.status],
      id: 'youtube',
      sortable: false,
      Cell: props => (
        <a href={props.value[0]} target='_blank'>
          <img
            src={props.value[1]}
            style={{
              width: '100%',
              maxWidth: '10em',
              maxHeight: '7em'
            }}
          />
        </a>
      ),
      maxWidth: 200
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tên video
        </span>
      ),
      accessor: d => [d.title, d._id],
      id: 'title',
      style: { overflow: 'visible', whiteSpace: 'normal', fontWeight: 'bold' },
      Cell: ({value}) => {
        return <Link to={"#"} onClick={()=>this.onChangeModal(value[1],'videos')} >{value[0]}</Link>;
      }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Thể loại
        </span>
      ),
      sortable: false,
      accessor: 'categoryNames',
      maxWidth: 150,
      style: { overflow: 'visible', whiteSpace: 'normal' }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Người đăng
        </span>
      ),
      accessor: 'user_id',
      // id: 'user_id',
      Cell: props => (
        
        <Link to={"#"} onClick={()=>this.onChangeModal(props.value._id,'users')}>
          <b>{props.value.name}</b>
        </Link>
      ),
      style: { overflow: 'visible', whiteSpace: 'normal', maxWidth: "100px" }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Lượt xem
        </span>
      ),
      accessor: 'views_count',
      Cell: props => (
        <span>{props.value}</span>
      ),
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Bình luận
        </span>
      ),
      accessor: 'comments_count',
      Cell: props => (
        <span>{props.value}</span>
      ),
    },
    {
      Header: 'Thời gian',
      accessor: 'createdAt',
      width: 100,
      Cell: ({ value }) => {
        return <Moment format='DD/MM/YYYY h:mm:ss A' date={value} style={{ display: 'block', whiteSpace: 'normal' }} />;
      }
    },
    {
      Header: 'Hành động',
      accessor: '_id',
      Cell : (props)=> {
        let {status} = props.original;
        let defaultSelected = statusObject[status].position;
        return (
          <DropDown
            variant = {statusObject[status].variant}
            data = {videoStatus}
            notShow = 'pending'
            defaultSelected = {defaultSelected}
            onChange={(index, status) => this.changeStatus(index, status, props.value, props.index)}
          />
        )
      }
    }
  ];
  render() {
    const { data, isLoading, columns, status, pages, pageSize, listCategories, categories, modalId, modalShow, modalComponent } = this.state;
    let categoryItems = [];
    listCategories.forEach((category, index)=>{
      categoryItems.push(
        <option value = {category.value} key={index}>{category.label}</option>
      )
    });

    let modalChild = [];
    if(modalComponent === 'videos'){
      modalChild.push(<Video video_id={modalId}></Video>);
    }
    else if(modalComponent === 'users'){
      modalChild.push(<UserDetail id={modalId}>/</UserDetail>);
    }
    
    return (
      <Page breadcrumbs={[{ name: 'Video', active: true }]} className='TablePost'>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>
                Danh sách video
                <Input
                  className=' ml-5 d-inline'
                  type='select'
                  name='status'
                  value={status}
                  style={{ width: 200 }}
                  onChange={this.onChange}
                >
                  <option value=''>Trạng thái</option>
                  <option value='pending'>Pending</option>
                  <option value='approved'>Approved</option>
                  <option value='rejected'>Rejected</option>
                </Input>
                <Input
                  className=' ml-5 d-inline'
                  type='select'
                  name='categories'
                  value={categories}
                  style={{ width: 200 }}
                  onChange={this.onChange}
                >
                  <option value=''>Danh mục</option>
                  {categoryItems}
                </Input>
              </CardHeader>
              
              <CardBody>
                <ReactTable
                  loading={isLoading}
                  manual
                  onFetchData={this.getData}
                  data={data}
                  columns={columns}
                  // pageSizeOptions={[Config.VIDEO_PER_PAGE]}
                  defaultPageSize={pageSize}
                  pages={pages}
                  resizable={false}
                  showPageJump={true}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
        <ModalHandle
          show={modalShow}
          onChange={()=>this.onCloseModal()}
        >
          {modalChild}
        </ModalHandle>

        <Toast
          ref={(ref) => this.toast = ref}
        />
      </Page>
    );
  }
}

export default TablePost;
