import Page from '../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import Api from '../utils/api';
import Utils from '../utils/utils';
import PeriodSelector from '../components/PeriodSelector';
import MModal from '../components/MModal';
import ApproveKYC from './ApproveKYC';
import UserDetail from './UserDetail';
import Notification from '../components/Toast';
import classnames from 'classnames';

class Table_User extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      showModal: false,
      page: 0,
      pages: null,
      pageSize: 10,
      user: {},
      status: '',
      modal: ''
    };
    this.periodDay = 7;
    this.getData = this.getData.bind(this);
  }

  columns = [
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Ảnh đại diện
        </span>
      ),
      accessor: 'avatar',
      width: 100,
      Cell: props => {
        return (
          <img
            className='border rounded-circle'
            width={56}
            height={56}
            src={Utils.getAvatarOfUser(props.value) || require('../assets/img/default_user_avatar.png')}
          />
        );
      }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tên
        </span>
      ),
      accessor: d => [d._id, d.name],
      width: 150,
      id: 'name',
      sortable: true,
      Cell: props => {
        return (
          <div>
            <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>
              <b
                className='text-primary c-pointer'
                onClick={() => {
                  this.setState({
                    showModal: true,
                    user: props.original,
                    modal: 'user'
                  });
                }}
              >
                {props.value[1]}
              </b>
            </div>
          </div>
        );
      }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Email
        </span>
      ),
      sortable: true,
      width: 200,
      accessor: 'email',
      Cell: props => <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>{props.value}</div>
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> SDT
        </span>
      ),
      width: 100,
      accessor: 'phone',
      Cell: props => <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>{props.value}</div>
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Ngày sinh
        </span>
      ),
      accessor: 'birthday',
      maxWidth: 100
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tổng tiền nạp
        </span>
      ),
      accessor: 'wallet',
      Cell: props => <span>{props.value && props.value.deposit}</span>
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tổng hoàn tiền
        </span>
      ),
      accessor: 'wallet',
      Cell: props => <span>{(props.value && props.value.refund) || 0}</span>
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tổng điểm
        </span>
      ),
      accessor: 'point',
      Cell: props => <span>{Utils.getHumanReadableNumber(props.value)}</span>
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Trạng thái
        </span>
      ),
      accessor: d => [d.status, d._id],
      id: 'status',
      Cell: props => {
        let text = 'Không KYC';
        let color = 'text-muted';
        if (props.value[0] === 'approve-kyc') {
          text = 'Đã xác thực';
          color = 'text-success';
        } else if (props.value[0] === 'reject-kyc') {
          text = 'Đã từ chối';
          color = 'text-danger';
        } else if (props.value[0] === 'pending-kyc') {
          text = 'Đang chờ';
          color = 'text-info';
        } else if (props.value[0] === 'disabled') {
          text = 'Vô Hiệu';
          color = 'text-muted';
        }
        return (
          <span
            className={`${color} c-pointer`}
            onClick={() =>
              props.value[0] !== 'disabled' && props.value[0] !== 'non-kyc' && this.setState({ showModal: true, user: props.original, modal: 'kyc' })
            }
          >
            {text}
          </span>
        );
      },
      filterable: true,
      Filter: ({ filter, onChange }) => (
        <select
          onChange={event => {
            this.setState({ status: event.target.value });
            onChange();
          }}
          style={{ width: '100%' }}
          value={filter ? filter.value : ''}
        >
          <option value=''>Tất cả</option>
          <option value='non-kyc'>Không KYC</option>
          <option value='approve-kyc'>Đã duyệt</option>
          <option value='reject-kyc'>Đã từ chối</option>
          <option value='pending-kyc'>Đang chờ</option>
          <option value='disabled'>Vô hiệu</option>
        </select>
      )
    },
    {
      Header: 'hành động',
      accessor: d => [d['_id'], d['status']],
      id: 'action',
      style: { display: 'flex', justifyContent: 'center' },
      Cell: props => (
        <>
          <Button
            className={classnames({
              'btn-danger': props.value[1] !== 'disabled',
              'btn-success': props.value[1] === 'disabled'
            })}
            style={{ width: '100%', height: '80%' }}
            onClick={() => {
              const user = props.original;
              const status =
                props.value[1] !== 'disabled'
                  ? 'disabled'
                  : user.passport_image && user.passport_image.length === 2 && user.selfy_image
                  ? 'pending-kyc'
                  : 'non-kyc';
              this.onChangeStatus(props.value[0], status);
            }}
          >
            {props.value[1] === 'disabled' ? 'Mở' : 'Đóng'}
          </Button>
        </>
      )
    }
  ];
  getData(state) {
    this.setState({
      isLoading: true,
      page: state.page,
      pageSize: state.pageSize
    });
    const sort = state.sorted && state.sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString();
    Api.getUsers({ page: state.page + 1, limit: state.pageSize, status: state.status || this.state.status, sort })
      .then(response => {
        if (response.success) {
          this.setState({
            isLoading: false,
            data: response.data,
            pages: response.totalPages
          });
        } else {
          this.notificationSystem.showMessage('failed');
          this.setState({
            isLoading: false,
            error: 'Tải dữ liệu lỗi.'
          });
        }
      })
      .catch(() => this.notificationSystem.showMessage('failed'));
  }
  onChangeStatus = (id, status) => {
    Api.updateUserStatus({ id, status })
      .then(res => {
        if (res.success) {
          this.onUpdateData(id, res.data);
        } else this.notificationSystem.showMessage('failed');
      })
      .catch(() => this.notificationSystem.showMessage('failed'));
  };
  onUpdateData = (id, updateData) => {
    const index = this.state.data.findIndex(el => el._id === id);
    const newData = [...this.state.data];
    newData[index] = updateData;
    this.setState({ data: newData });
  };
  onChangePeriod(_itemp) {
    this.setState({
      isLoading: true,
      data: []
    });
    if (_itemp.value == 0) {
      Api.getUsers()
        .then(response => {
          if (response.success) {
            const data = response.data.map(item => {
              return item;
            });
            this.setState({
              isLoading: false,
              data
            });
          } else {
            this.notificationSystem.showMessage('failed');
            this.setState({
              isLoading: false,
              error: 'Tải dữ liệu lỗi'
            });
          }
        })
        .catch(() => this.notificationSystem.showMessage('failed'));
    } else {
      let end = new Date();
      let start = end.getTime() - _itemp.value * 60 * 60 * 24 * 1000;
      Api.getUsersBetweenPeriod(start, end, {})
        .then(response => {
          if (response.success) {
            const data = response.data.map(item => {
              return item;
            });
            this.setState({
              isLoading: false,
              data
            });
          } else {
            this.notificationSystem.showMessage('failed');
            this.setState({
              isLoading: false,
              error: 'Tải dữ liệu lỗi'
            });
          }
        })
        .catch(() => this.notificationSystem.showMessage('failed'));
    }
  }
  onClose = () => {
    this.setState({
      showModal: false
    });
  };
  render() {
    const { data, isLoading, user, page, pageSize, pages, modal } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'tables', active: true }]} className='TablePost'>
        <Notification ref={notificationSystem => (this.notificationSystem = notificationSystem)} />
        <MModal showModal={this.state.showModal} onClose={this.onClose} title={modal.toUpperCase()}>
          {modal === 'kyc' && <ApproveKYC user={user} onReload={this.onReload} onUpdateData={this.onUpdateData} onClose={this.onClose} />}
          {modal === 'user' && <UserDetail user_id={user._id} />}
        </MModal>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>
                <div className='row mx-0 col-12 align-items-center'>
                  <label className='mb-0'>Danh sách thành viên</label>
                  <div>
                    <PeriodSelector
                      data={[
                        { name: 'Tất cả', value: 0 },
                        { name: 'Hôm nay', value: 1 },
                        { name: '7 ngày', value: 7 },
                        { name: '30 ngày', value: 30 },
                        { name: '6 tháng', value: 180 },
                        { name: '1 năm', value: 365 }
                      ]}
                      initialIndex={0}
                      onChange={(index, value) => this.onChangePeriod(value)}
                    />
                  </div>
                </div>
              </CardHeader>
              <CardBody>
                <ReactTable
                  className='overflow-hidden'
                  columns={this.columns}
                  manual
                  data={data}
                  resolveData={data =>
                    data.map(item => {
                      item.fullname = { name: item.name, realName: item.real_name };
                      item.id = item._id;
                      return item;
                    })
                  }
                  page={page}
                  loading={isLoading}
                  onFetchData={this.getData}
                  filterable={false}
                  pageSize={pageSize}
                  pages={pages}
                  defaultPageSize={10}
                  sortable={false}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Page>
    );
  }
}

export default Table_User;
