import React from 'react'
import Switch from "react-switch";

class ListPermission extends React.Component{
    constructor(props){
        super(props);
    }

    changePermission(index){
        const permissions = this.props.permissions || [];
        let activedPermissions = this.props.activedPermissions || [];
        let checked = !(!activedPermissions.find(item=>item=== permissions[index]._id));
        if(checked){
            activedPermissions = activedPermissions.filter(item=>item !== permissions[index]._id);
        } else{
            activedPermissions.push(permissions[index]._id);
        }

        if(this.props.onChange){
            this.props.onChange(activedPermissions);
        }
    }

    render(){
        var listShow = [];
        const permissions = this.props.permissions || [];
        const activedPermissions = this.props.activedPermissions || [];
        if(permissions && permissions.length > 0){
            for(let i = 0; i< permissions.length; i++){
                let checked = !(!activedPermissions.find(item=>item === permissions[i]._id));
                listShow.push(
                    <tr key={permissions[i]._id}>
                        <th scope="row">{i}</th>
                        <td>{permissions[i].description}</td>
                        <td> <Switch 
                        onChange={()=>this.changePermission(i)} 
                        checked={checked} 
                        height={20}
                        width={48}/></td>
                    </tr>
                    
                )
            }
        }
        return (
            <table className="table border">
                <thead>
                    <tr>
                        <th scope="col">{"#"}</th>
                        <th scope="col">{"Quyền"}</th>
                        <th scope="col">{"Trạng thái"}</th>                        
                    </tr>
                </thead>
                <tbody>
                    {listShow}
                </tbody>
            </table>
        )
    }
}

export default ListPermission;