import React from 'react';
import {Button} from 'reactstrap';

export default class Selector extends React.Component{
    constructor(props){
        super(props);
        let selectedIndex = this.props.defaultIndex || 0;
        this.state = {
            selectedIndex
        }
    }

    select(index, data){
        if(index !== this.state.selectedIndex) this.setState({selectedIndex: index});
        if(this.props.onChange){
            this.props.onChange(index, data);
        }
    }

    render(){
        let data = this.props.data || []
        let listButton = [];
        for(let i = 0; i < data.length; i++){
            let color = "secondary";
            if(i === this.state.selectedIndex){
                color="success";
            }
            listButton.push(
                <Button
                    color={color}
                    onClick={()=>this.select(i, data[i])}
                    style={{
                        "minWidth":"200px"
                    }}
                    className="mx-2"
                >
                    {data[i]}
                </Button>
            )

        }
        return(
            <div className="">
                {listButton}
            </div>
        )
    }
}