import React from 'react';
import {Form, FormGroup, Input, Label} from 'reactstrap';
import { Button } from 'react-bootstrap';



const containerStyle = {
    "display": "flex",
    "justifyContent": "center",
    "justifyItems" : "center"
}
export default class UserLogin extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            user:'',
            password: '',
            // recaptchaVerifier: null;
        }
        // this.handleChange = this.handleChange.bind(this);
    }

    handelChange (key, event){
        this.setState({ [key]: event.target.value });
    }

    handleSubmit = event => {
        if(this.props.onChange){
            let user = this.state.user;
            let password = this.state.password;
            this.props.onChange(user, password);
            event.preventDefault();
        }
    };

    componentDidMount(){
       
    }

    
    render(){
        // window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier('recaptcha-container');
        // if()
        return (
            <div style={containerStyle}>
                <div className="col-md-6">
                    <div className="card card-body">
                        <Form>
                            <FormGroup>
                                <Label for="user">{"Email hoặc số điện thoại"}</Label>
                                <Input type="user" name="user" placeholder="Email hoặc số điện thoại" value={this.state.user} onChange={(event) =>  this.handelChange('user',event)}></Input>
                            </FormGroup>
                            <FormGroup>
                                <Label for="password">{"Password"}</Label>
                                <Input type="password" name="password" placeholder="password" value={this.state.password} onChange={(event) => this.handelChange('password',event)}></Input>
                            </FormGroup>
                            <Button
                                // type="submit"
                                // value={this.renderButtonText()}
                                bsSize="lg"
                                className="bg-gradient-theme-left border-0 text-white"
                                block
                                onClick={this.handleSubmit}>
                                    Đăng nhập
                            </Button>
                        </Form>
                    </div>
                </div>
                
            </div> 
               
        );
    }
}