import Page from '../../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table, Button } from 'reactstrap'; 
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import cellEditFactory from 'react-bootstrap-table2-editor';
import { Link } from 'react-router-dom';

import Api from '../../utils/api';
import Utils from '../../utils/utils';
import queryParser from 'query-string';
import Config from '../../utils/config';

class Reward extends React.Component {
  columns = [
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Ảnh
        </span>
      ),
      accessor: 'image',
      width: 100,
      Cell: props => {
        return <img className='border ' width={56} height={56} src={Utils.getFullUrl(props.value)} />;
      }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tên quà tặng
        </span>
      ),
      accessor: 'name',
      Cell: props => (
        <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>
          <b>{props.value}</b>
        </div>
      )
    },
    {
      Header: 'Số lượng',
      accessor: 'amount',
      width: 100,
      Cell: props => {
        let color = '';
        if (props.value.remain <= 0) {
          color = 'text-danger';
        }
        return <div className={color}>{`${props.value.remain}/${props.value.total}`}</div>;
      }
    },
    {
      Header: 'Bắt đầu',
      accessor: 'start_time',
      width: 100,
      Cell: props => (
        <div>
          <div>{Utils.convertTimeToYYYYMMDDString(props.value)}</div>
          <div>{Utils.convertTimeToHHMMString(props.value)}</div>
        </div>
      )
    },
    {
      Header: 'Kết thúc',
      accessor: 'end_time',
      width: 100,
      Cell: props => (
        <div>
          <div>{Utils.convertTimeToYYYYMMDDString(props.value)}</div>
          <div>{Utils.convertTimeToHHMMString(props.value)}</div>
        </div>
      )
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Trạng thái
        </span>
      ),
      width: 150,
      accessor: 'status',
      Cell: props => {
        let text = 'Kích hoạt';
        let color = 'text-success';
        if (props.value === 'disable') {
          text = 'Không kích hoạt';
          color = 'text-danger';
        }

        return <span className={color}>{text}</span>;
      }
    },
    {
      Header: 'Thao tác',
      accessor: '_id',
      maxWidth: 150,
      Cell: props => (
        <div className='row mx-0 justify-content-end'>
          <div className='mx-2 my-2'>
            <Link to={`/update-reward?id=${props.value}`}>
              <Button color='warning'>Sửa</Button>
            </Link>
          </div>
          {/* <div className="mx-2 my-2">
                    <Button onClick={() => this.updateCategory(props.value, 'active')}
                        color="success">Kích hoạt</Button>
                </div>
                <div className="mx-2 my-2">
                    <Button onClick={() => this.updateCategory(props.value, 'disable')}
                        color="danger">Vô hiệu</Button>
                </div> */}
        </div>
      )
    }
  ];

  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      name: '',
      status: 'default',
      id: null
    };

    this.getData();
  }

  componentWillReceiveProps(nextProps) {
    this.getData();
  }

  getData() {
    this.setState({
      isLoading: true,
      data: []
    });

    Api.getRewards().then(response => {
      if (response.success) {
        const data = response.data.map(item => {
          return item;
        });

        this.setState({
          isLoading: false,
          data
        });
      } else {
        this.setState({
          isLoading: false,
          error: 'Tải dữ liệu lỗi.'
        });
      }
    });
  }

  onChangeField(key, name) {
    this.setState({ [key]: name });
  }

  updateCategory(id, status) {
    const item = this.state.data.find(item => item._id === id);
    if (!item) {
      return;
    }

    if (status) {
      this.updateCategoryOnServer(id, item.name, status);
    } else {
      this.setState({
        id: item._id,
        name: item.name,
        status: item.status
      });
    }
  }

  updateCategoryOnServer(id, name, status) {
    Api.updateCategory({ id, name, status }).then(response => {
      if (response.success) {
        this.setState({
          isLoading: false,
          data: this.state.data.map(item => {
            if (item._id == id) {
              item.name = name;
              item.status = status;
            }
            return item;
          }),
          name: '',
          status: 'default',
          id: null
        });
      } else {
        this.setState({ isLoading: false, error: 'Sửa quà tặng thất bại' });
      }
    });
  }

  onSubmit() {
    const { id, name, status } = this.state;
    this.setState({ isLoading: true, error: null });
    if (id) {
      this.updateCategoryOnServer(id, name, status);
    } else {
      Api.createCategory({ name, status }).then(response => {
        if (response.success) {
          this.setState({
            isLoading: false,
            data: [...this.state.data, response.data],
            name: '',
            status: 'default',
            id: null
          });
        } else {
          this.setState({ isLoading: false, error: 'Thêm quà tặng thất bại' });
        }
      });
    }
  }

  getType(query) {
    const parsed = queryParser.parse(query);
    return parsed.type;
  }

  render() {
    const { isLoading, data, name, status, id } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'tables', active: true }]} className='TablePost'>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>Quà tặng</CardHeader>
              <CardBody>
                <div className='form-inline justify-content-end mb-3'>
                  <Link to={Config.HOST_PREFIX + '/create-reward'}>
                    <button className='btn btn-success'>Thêm</button>
                  </Link>
                </div>
                <ReactTable
                  data={data}
                  resolveData={data =>
                    data.map(item => {
                      item.amount = { remain: item.remain, total: item.total };
                      return item;
                    })
                  }
                  loading={isLoading}
                  columns={this.columns}
                  defaultPageSize={15}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Page>
    );
  }
}

export default Reward;
