import React from 'react';
import {Button, Modal} from 'react-bootstrap';

export default class ModalHandle extends React.Component{
    constructor(props, context) {
        super(props, context);
        let show = this.props.show || false;
        let id = this.props.id || null;
        let component = this.props.component || null;
        this.handleShow = this.handleShow.bind(this);
        this.handleClose = this.handleClose.bind(this);
    
        this.state = {
          show,
          id,
          component
        };
      }

      componentWillReceiveProps(nextProps){
          let show = nextProps.show !== this.state.show ? nextProps.show : this.state.show;
          let id = nextProps.id !== this.state.id ? nextProps.id : this.state.id;
          let component = nextProps.component !== this.state.component ? nextProps : this.state.component;
          this.setState({show, id, component});
      }
    
      handleClose() {
          if(this.props.onChange){
            this.props.onChange();
          }
      }
    
      handleShow() {
        this.setState({ show: true });
      }
    
      render() {
        return (
          <>
            <Modal show={this.state.show} onHide={this.handleClose}>
              <Modal.Body>
                  {this.props.children}
                  {/* {this.state.component} */}
              </Modal.Body>
              <Modal.Footer>
                <Button variant="secondary" onClick={this.handleClose}>
                  Đóng
                </Button>
              </Modal.Footer>
            </Modal>
          </>
        );
      }
}