import Page from '../../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button, Input } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import Api from '../../utils/api';
import Utils from '../../utils/utils';
import { Link } from 'react-router-dom';
import classnames from 'classnames';
import Notification from '../../components/Toast';
import MModal from '../../components/MModal';
import Store from './Store';
import Staff from '../employees/Staff';
import Config from '../../utils/config';

class TableCompany extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      error: null,
      pages: null,
      page: 0,
      pageSize: 10,
      columns: this.defaultColumns,
      status: '',
      showModal: false,
      modal: '',
      company_id: ''
    };
  }

  getData = state => {
    const sort = state.sorted && state.sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString();
    this.setState({
      isLoading: true,
      page: state.page,
      pageSize: state.pageSize
    });
    Api.getCompanies({
      status: state.status || this.state.status,
      page: state.page + 1,
      limit: state.pageSize,
      sort,
      select: 'images name total_store total_staff total_revenue status'
    }).then(response => {
      if (response.success) {
        this.setState({
          isLoading: false,
          pages: response.total_page,
          data: response.data
        });
      } else {
        this.setState({
          isLoading: false,
          error: 'Tải dữ liệu lỗi.'
        });
      }
    });
  };

  onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
    this.getData({ ...this.state, status: e.target.value });
  };

  defaultColumns = [
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Ảnh
        </span>
      ),
      accessor: d => [d.images, d.status],
      id: 'image',
      sortable: false,
      Cell: props => {
        return (
          <img
            src={Utils.getFullUrl(props.value[0][0]) ? Utils.getFullUrl(props.value[0][0]) : ''}
            style={{
              width: '100%',
              maxWidth: '10em',
              maxHeight: '7em'
            }}
          />
        );
      },
      maxWidth: 200
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tên DN
        </span>
      ),
      accessor: d => [d.name, d._id],
      id: 'name',
      Cell: ({ value }) => {
        return (
          <b style={{ display: 'block', overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }} title={value[0]}>
            {value[0]}
          </b>
        );
      }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Danh sách cửa hàng
        </span>
      ),
      width: 150,
      accessor: 'total_store',
      Cell: props => {
        return (
          <b
            className={classnames({
              'text-primary': props.value,
              'c-pointer': props.value
            })}
            onClick={() => this.setState({ showModal: true, company_id: props.original._id, modal: 'store' })}
          >
            {props.value ? props.value : 0}
          </b>
        );
      }
    },
    {
      Header: 'Tổng nhân viên',
      accessor: 'total_staff',
      width: 150,
      Cell: props => {
        return (
          <b
            className={classnames({
              'text-primary': props.value,
              'c-pointer': props.value
            })}
            onClick={() => this.setState({ showModal: true, company_id: props.original._id, modal: 'staff' })}
          >
            {props.value}
          </b>
        );
      }
    },
    {
      Header: 'Tổng doanh thu',
      maxWidth: 150,
      accessor: 'total_revenue',
      Cell: ({ value }) => {
        return value;
      }
    },
    {
      Header: 'Xác Minh',
      maxWidth: 100,
      accessor: 'status',
      Cell: ({ value }) => {
        return value === 'approved' ? 'active' : value;
      },
      filterable: true,
      Filter: ({ filter, onChange }) => (
        <select
          onChange={event => {
            this.setState({ status: event.target.value });
            onChange();
          }}
          style={{ width: '100%' }}
          value={filter ? filter.value : ''}
        >
          <option value=''>Tất cả</option>
          <option value='approved'>Đã duyệt</option>
          <option value='rejected'>Đã từ chối</option>
          <option value='pending'>Đang chờ</option>
          <option value='disabled'>Vô hiệu</option>
        </select>
      )
    },
    {
      Header: 'Hoạt động',
      maxWidth: 125,
      style: { display: 'flex', justifyContent: 'center' },
      id: 'act',
      accessor: d => [d._id, d.status],
      Cell: ({ value }) => {
        return (
          <Input
            className={classnames({
              'bg-success': value[1] === 'approved',
              'bg-warning': value[1] === 'pending',
              'bg-dark': value[1] === 'disabled',
              'bg-danger': value[1] === 'rejected',
              'text-white': value[1] !== 'pending'
            })}
            type='select'
            name='status'
            id='exampleSelect'
            value={value[1]}
            onChange={e => this.onApproveCompany(value[0], e.target.value)}
          >
            <option value='approved'>Kích hoạt</option>
            <option disabled value='pending'>
              Đang chờ
            </option>
            <option value='rejected'>Từ chối</option>
            <option value='disabled'>Vô hiệu</option>
          </Input>
        );
      }
    }
  ];
  onApproveCompany = (id, status) => {
    Api.approveCompany({ id, status })
      .then(res => {
        if (res.success) {
          const index = this.state.data.findIndex(el => el._id === id);
          const newData = [...this.state.data];
          if (index >= 0) {
            newData[index] = res.data;
            this.setState({ data: newData });
            this.notificationSystem.showMessage('Cập nhật thành công!');
          }
        } else this.notificationSystem.showMessage('failed');
      })
      .catch(err => this.notificationSystem.showMessage('failed'));
  };
  onClose = () => {
    this.setState({
      showModal: false
    });
  };
  render() {
    const { data, isLoading, columns, pages, page, pageSize, modal, company_id } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'Company', active: true }]} className='TableCompany'>
        <Notification ref={notificationSystem => (this.notificationSystem = notificationSystem)} />
        <MModal showModal={this.state.showModal} onClose={this.onClose} title={modal.toUpperCase()}>
          {modal === 'store' && <Store company_id={company_id} />}
          {modal === 'staff' && <Staff company_id={company_id} />}
        </MModal>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>Danh sách doanh nghiệp</CardHeader>
              <Row />
              <CardBody>
                <ReactTable
                  loading={isLoading}
                  manual
                  onFetchData={this.getData}
                  data={data}
                  columns={columns}
                  defaultPageSize={10}
                  pages={pages}
                  pageSize={pageSize}
                  page={page}
                  resizable={false}
                  showPageJump={true}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Page>
    );
  }
}

export default TableCompany;
