import React from 'react';

export class PeriodSelector extends React.Component {
    defaultPeriods = [
        { name: 'Hôm nay', value: 1, },
        { name: '7 ngày', value: 7, },
        { name: '30 ngày', value: 30, },
        { name: '6 tháng', value: 180, },
        { name: '1 năm', value: 365, },
    ];

    constructor(props) {
        super(props);
        let widthPercent = props.widthPercent ? props.widthPercent : "unset";
        let activeIndex = props.initialIndex >= 0 ? props.initialIndex : -1;
        this.state = {
            activeIndex,
            widthPercent
        };
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.initialIndex !== this.props.initialIndex) {
            let activeIndex = nextProps.initialIndex >= 0 ? nextProps.initialIndex : -1;
            this.setState({ activeIndex });
        }
    }

    select(index) {
        if (index != this.state.activeIndex) {
            this.setState({ activeIndex: index });
            if (this.props.onChange) {
                const periods = this.props.data && this.props.data.length > 0 ? this.props.data : this.defaultPeriods;
                this.props.onChange(index, periods[index]);
            }
        }
    }

    render() {
        const periodViews = [];
        const periods = this.props.data && this.props.data.length > 0 ? this.props.data : this.defaultPeriods;
        for (let i = 0; i < periods.length; i++) {
            let bgColor = '#ccc';
            let textColor = 'white';
            if (i === this.state.activeIndex) {
                bgColor = '#3b9aba';
                textColor = 'white';
            }

            periodViews.push(
                <div style={{
                    cursor: 'pointer',
                    borderRadius: 8,
                    color: textColor,
                    backgroundColor: bgColor,
                    textTransform: 'none',
                    flex: this.props.widthPercent,
                    textAlign: "center"
                    
                }}
                    onClick={() => this.select(i)} className={`mx-3 px-3  my-1`}>
                    {periods[i].name}
                </div>
            );
        }
        return (
            <div className="col-12 row mx-0 px-0">
                {periodViews}
            </div>
        );
    }
}

export default PeriodSelector;