import React from 'react';
import { Col, Row, Button } from 'react-bootstrap';
import {Form, FormGroup, Label, CustomInput, Input} from 'reactstrap';
import Api from '../../utils/api';
import apiUser from '../../utils/api.user';
import UserChoosed from './UserChoosed';
import UserLogin from './UserLogin';
import stateManager from '../../stateManager/stateManager';
import Toast from '../../components/Toast';
import Selector from '../../components/Selector';
import ListVideoUpload from './ListVideoUpload';
import UploadYoutubeTool from './UploadYoutubeTool';
import TableListVideoYoutube from './TableListVideoYoutube';

const style = {
    minHeight: "85vh",
}

class UploadVideo extends React.Component{
    constructor(props){
        super(props);
        let listCategories = [];
        let listUser = stateManager.getState().users || [];
        
        this.state = {
            listCategories,
            listUser,
            toLogin: false,
            choosedUser: -1,
            link: '',
            selectRender: 0,
            personalVideos:[],
            totalPagePersonalVideos: null,
            listYoutubeVideoGetted:[]
        }

        Api.getCategory('active').then(response => {
            if(response.success){
                this.setState({listCategories: response.data.map(item => {item.checked= false; return item})});
            }
            else {
                this.toast.showMessage('get categories error');
            }
        });
    }



    onChangeUser(index, data){
        if(index === 'new'){
            if(!this.state.toLogin){
                this.setState({toLogin:true});
            }
        }
        else{
            if(this.state.choosedUser !== index){
                this.setState({choosedUser:index});
                let listUser = this.state.listUser;
                apiUser.setToken(listUser[index].access_token);
                
                if(this.state.selectRender === 1){
                    apiUser.getPersonalVideos({limit:5, page:1}).then(response=>{
                        if(response.success){
                            let personalVideos = response.data;
                            this.setState({personalVideos, totalPagePersonalVideos: response.totalPage});
                        }
                        else{
                            this.toast.showMessage("Lỗi");
                        }
                    });
                }
                
            }
            
        }
        
    }

    async userLogin(user, password){
        if(user.length > 0 && password.length > 0){
            let data = {
                user: user,
                password: password
            }
            apiUser.login(data).then(response => {
                if(response.success){
                    let user = response.data;
                    apiUser.setToken(response.data.access_token);
                    let users = stateManager.getState().users || [];
                    users.push(user);
                    stateManager.notice({users});
                    this.setState({toLogin: false});
                }
                else{
                    this.toast.showMessage('sai ten dang nhap hoặc mật khẩu');
                }
            });
            
        }
        else{
            this.toast.showMessage('user hoặc mật khẩu trống ')
        }

    }

    changeCategoryState(index, checked){
        let listCategories = [...this.state.listCategories];
        listCategories[index].checked = checked;
        this.setState({listCategories});
    }

    changeLinkInputState(input){
        this.setState({link: input.target.value.toString()});
    }

    handleSubmit= event => {
        let listCategoriesChecked = [];
        let listCategories = this.state.listCategories;
        for(let i = 0; i < listCategories.length; i++){
            if(listCategories[i].checked) listCategoriesChecked.push(listCategories[i]._id);
        }
        let data = {
            link: this.state.link,
            categories: listCategoriesChecked.toString()
        }
        apiUser.createVideo(data).then(response =>{
            if(response.success){
                this.setState({link:''});
                this.toast.showMessage('đăng video thành công');
            }
            else {
                this.toast.showMessage('submit fail');
            }
        });
        event.preventDefault();
    }

    setDataListYoutubeVideoGetted(data){
        this.setState({listYoutubeVideoGetted: data});
    }

    selectRender(index,data){
        if(index !== this.state.selectRender) this.setState({selectRender: index});
        if(index === 1){
            apiUser.getPersonalVideos({limit:5, page:1}).then(response=>{
                if(response.success){
                    this.setState({personalVideos:response.data});
                }
                else{
                    this.toast.showMessage("Đã xảy ra lỗi");
                }
            })
        }
    }

    render(){
        let {listYoutubeVideoGetted,personalVideos, listUser, listCategories, toLogin, choosedUser, link, selectRender} = this.state;
        
        var CategoriesCheckbox = [];
        for(let i = 0; i < listCategories.length; i++){
            CategoriesCheckbox.push(
                <CustomInput
                // width={width}
                id={listCategories[i]._id}
                type="checkbox" 
                // label={listCategories[i].name} 
                onChange={event => this.changeCategoryState(i,event.target.checked)}  
                name={listCategories[i]._id}
                checked={listCategories[i].checked}
                value={listCategories[i].name}
                >{listCategories[i].name}</CustomInput>
            )
        }
        let contentToChange=[];
        let youtubeVideoGetted = [];
        
        if(!toLogin && choosedUser > -1){
            let videoViewer = '';
            let contentViewer = [];
            if(selectRender === 0){
                if(link.indexOf('https://www.youtube.com/watch') >= 0){
                    let indexSub = link.indexOf('v=') + 2;
                    if(indexSub > 1){
                        let subStringVideo = link.substring(indexSub, link.length);
                        videoViewer= [
                            <iframe 
                                style={{"width":"100%"}}
                                // width="560" 
                                height="315" 
                                src={"https://www.youtube.com/embed/" + subStringVideo} 
                                frameborder="0" 
                                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
                                allowfullscreen>
                            </iframe>
                        ]
                    }
                    else {
                        videoViewer="sai link video mất rồi @@"
                    }
                }
                contentViewer.push(
                    <Row className="mx-0 px-0">
                        <Col md={6}>
                            <Form>
                                <FormGroup>
                                    <Label>Links</Label>
                                    <Input type="text" name="link" value={link} placeholder="links" onChange={input => this.changeLinkInputState(input)}/>
                                </FormGroup>
                                <FormGroup>
                                <Label>Thể loại</Label>
                                <FormGroup>
                                    {CategoriesCheckbox}
                                    </FormGroup>
                                </FormGroup>
                                <Button variant="primary" onClick={this.handleSubmit}>
                                    Submit
                                </Button>
                            </Form>
                        </Col>
                        <Col md={6}>
                            {videoViewer}
                        </Col>
                    </Row>
                )
            }
            else if(selectRender === 1){
                contentViewer.push(
                    <ListVideoUpload
                        data={personalVideos}
                        // pages={Math.ceil(personalVideos.length/15)}
                    />
                )
            }
            else if(selectRender === 2){
                contentViewer.push(
                    <UploadYoutubeTool
                        onChange={(data)=>this.setDataListYoutubeVideoGetted(data)}
                        // listUser={listUser}
                    />
                )

                youtubeVideoGetted.push(
                    <TableListVideoYoutube
                        data={listYoutubeVideoGetted}
                        listUser={listUser}
                    />
                )
            }
            
            
            contentToChange = [
                <div>
                <Selector
                    data={["Đăng video","Danh sách đã đăng","Super tool"]}
                    // defaultIndex = {0}
                    onChange={(index,data) => this.selectRender(index,data)}
                />
                {contentViewer}
                </div>
            ];
        }
        else if(toLogin){
            contentToChange = <UserLogin onChange={(user, password)=> this.userLogin(user, password)} />
        }
        else contentToChange = "you need to chose user to use this page";

        
        return(
            <div style={style}>
            <Row>
                <Col className="" md="10">
                   {contentToChange}
                </Col>
                
            
                <Col md={2}>
                    <UserChoosed 
                        listUser={listUser}
                        onChange={(index, data)=> this.onChangeUser(index, data)}
                    >

                    </UserChoosed>
                </Col>
                
            </Row>
            <Row>
                {youtubeVideoGetted}
            </Row>
            <Toast
                ref={(ref) => this.toast = ref}
            />
            </div>
        );
    }
}

export default UploadVideo;