import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Row, Col, FormGroup, Label, CustomInput, Button } from 'reactstrap';
import api from '../utils/api';
import Notification from '../components/Toast';
import ImageAsync from '../components/ImageAsync';

export default class ApproveKYC extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: this.props.user.id,
      selfy_status: (this.props.user.selfy_image && this.props.user.selfy_image.status) || '',
      front_passport_status: (this.props.user.passport_image[0] && this.props.user.passport_image[0].status) || '',
      backside_passport_status: (this.props.user.passport_image[1] && this.props.user.passport_image[1].status) || ''
    };
  }
  onChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  onSave = () => {
    api
      .approveKYC(this.state)
      .then(res => {
        if (res.success) {
          this.showMessage('cập nhật thành công!!');
          // this.props.onReload();
        } else this.showMessage('cập nhật thất bại!!');
        this.props.onUpdateData(this.state.id, res.data);
      })
      .catch(err => {
        this.showMessage('cập nhật thất bại!!');
      });
  };
  showMessage(message, level = 'info') {
    this.notificationSystem.showMessage(message, level);
  }

  render() {
    const { user } = this.props;
    return (
      <>
        <Col className='mb-0'>
          <Row className='ml-2'>
            <Col md='5' className='py-3'>
              Họ tên: {user.name}
            </Col>
            <Col md='7' className='py-3'>
              Địa chỉ: {user.address}
            </Col>
            {user.status === 'non-kyc' ? (
              <div style={{ padding: 15 }}>Chưa cập nhật KYC</div>
            ) : (
              <>
                <Col md='3' className='mb-0'>
                  <Notification ref={notificationSystem => (this.notificationSystem = notificationSystem)} />
                  <FormGroup>
                    <Label for='exampleCustomSelect'>Selfy&nbsp;</Label>
                    <CustomInput
                      name='selfy_status'
                      value={this.state.selfy_status}
                      type='select'
                      id='select'
                      style={{ width: '70%' }}
                      onChange={this.onChange}
                    >
                      <option value='approve'>approve</option>
                      <option value='pending' disabled>
                        pending
                      </option>
                      <option value='reject'>reject</option>
                    </CustomInput>
                  </FormGroup>
                  {user.selfy_image && <ImageAsync path={user.selfy_image.path} />}
                </Col>
                <Col md='3' className='mb-0'>
                  <FormGroup>
                    <Label for='exampleCustomSelect'>Front&nbsp;</Label>
                    <CustomInput
                      name='front_passport_status'
                      value={this.state.front_passport_status}
                      type='select'
                      id='select'
                      style={{ width: '70%' }}
                      onChange={this.onChange}
                    >
                      <option value='approve'>approve</option>
                      <option value='pending' disabled>
                        pending
                      </option>
                      <option value='reject'>reject</option>
                    </CustomInput>
                  </FormGroup>
                  {user.passport_image[0] && <ImageAsync path={user.passport_image[0].status} />}
                </Col>
                <Col md='3' className='mb-0'>
                  <FormGroup>
                    <Label for='exampleCustomSelect'>Back&nbsp;</Label>
                    <CustomInput
                      name='backside_passport_status'
                      value={this.state.backside_passport_status}
                      type='select'
                      id='select'
                      style={{ width: '70%' }}
                      onChange={this.onChange}
                    >
                      <option value='approve'>approve</option>
                      <option value='pending' disabled>
                        pending
                      </option>
                      <option value='reject'>reject</option>
                    </CustomInput>
                  </FormGroup>
                  {user.passport_image[1] && <ImageAsync path={user.passport_image[1].status} />}
                </Col>
                <Col md='3' className='mb-0'>
                  <div>
                    <Button className='mr-5' onClick={this.onSave} color='info'>
                      Lưu
                    </Button>
                  </div>
                </Col>
              </>
            )}
          </Row>
        </Col>
      </>
    );
  }
}
