import React from 'react';

export class TypeTimeStatisticSelector extends React.Component {
    defaultType = [
        { name: 'Giờ', value: 'hour', },
        { name: 'Ngày', value: 'day', },
        { name: 'Tuần', value: 'week', },
        { name: 'Tháng', value: 'month', }
    ];

    constructor(props) {
        super(props);
        let timeType = props.timeType ? props.timeType : this.defaultType;
        let activeIndex = props.initialIndex >= 0 ? props.initialIndex : -1;
        this.state = {
            activeIndex,
            timeType
        };
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.initialIndex !== this.props.initialIndex) {
            let activeIndex = nextProps.initialIndex >= 0 ? nextProps.initialIndex : -1;
            this.setState({ activeIndex });
        }
        if(nextProps.timeType !== this.props.timeType){
            
            this.setState({timeType: nextProps.timeType});
        }
    }

    select(index) {
        if (index != this.state.activeIndex) {
            this.setState({ activeIndex: index });
            if (this.props.onChange) {
                const type = this.props.data && this.props.data.length > 0 ? this.props.data : this.state.timeType;
                this.props.onChange(index, type[index]);
            }
        }
    }

    render() {
        const statisticViews = [];
        const type = this.props.data && this.props.data.length > 0 ? this.props.data : this.state.timeType;
        for (let i = 0; i < type.length; i++) {
            let bgColor = '#ccc';
            let textColor = 'white';
            if (i === this.state.activeIndex) {
                bgColor = '#3b9aba';
                textColor = 'white';
            }

            statisticViews.push(
                <div style={{
                    cursor: 'pointer',
                    borderRadius: 8,
                    color: textColor,
                    backgroundColor: bgColor,
                    textTransform: 'none',
                    flex: 0.25,
                    textAlign: "center"
                }}
                    onClick={() => this.select(i)} className={`mx-3 px-3  my-1`}>
                    {type[i].name}
                </div>
            );
        }
        return (
            <div className="col-12 row mx-0 px-0">
                {statisticViews}
            </div>
        );
    }
}

export default TypeTimeStatisticSelector;