import React from 'react';
import {
    Card,
    CardBody,
    CardHeader,
} from 'reactstrap';
import _ from 'lodash';
import api from '../../utils/api';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Toast from '../../components/Toast';
import stateManager from '../../stateManager/stateManager';

export class Account extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isFetching: true,
            user: { ...stateManager.getState() },
            oldPassword: '',
            newPassword: '',
            confirmPassword: '',
            oldPasswordError: null,
            newPasswordError: null,
            confirmPasswordError: null,
        }
    }

    onChangeField(key, name) {
        const errorKey = key + 'Error';
        this.setState({ [key]: name, [errorKey]: null });
    }

    changePassword() {
        const {
            oldPassword,
            newPassword,
            confirmPassword,
        } = this.state;

        this.setState({
            oldPasswordError: null,
            newPasswordError: null,
            confirmPasswordError: null,
        });

        const error = {};
        if (oldPassword.length < 5) {
            error.oldPasswordError = 'Mật khẩu không đúng.';
        }

        if (newPassword.length < 6) {
            error.newPasswordError = 'Mật khẩu có ít nhất 6 ký tự.';
        }

        if (confirmPassword.length < 6) {
            error.confirmPasswordError = 'Nhập lại mật khẩu không chính xác.';
        }

        if (!_.isEmpty(error)) {
            this.setState(error);
            return;
        }

        api.changePassword({oldPassword,newPassword}).then(response=>{
            if(response.success){
                this.showMessage("Cập nhật mật khẩu thành công");
                this.setState({oldPassword: '', newPassword: '', confirmPassword: ''});
            } else{
                this.showMessage("Cập nhật mật khẩu thất bại");
            }
        });
    }

    showMessage(msg){
        this.toast.showMessage(msg);
    }

    render() {
        const {
            user,
            oldPassword,
            newPassword,
            confirmPassword,
            oldPasswordError,
            newPasswordError,
            confirmPasswordError,
        } = this.state;

        let statusText = 'Hoạt động';
        let statusClass = 'text-success';
        if (user.status === 'disable') {
            statusText = 'Bị khóa';
            statusClass = 'text-danger';
        }
        return (
            <div className="col-12">
                <Card className="mb-3">
                    <CardHeader>
                        Thông tin admin
                </CardHeader>
                    <CardBody>
                        <div className="col-12 row">
                            <img
                                className="border rounded-circle"
                                src={require('../../assets/img/default_user_avatar.png')} width="56" height="56" />
                            <div className="px-3 text-muted">
                                <div>
                                    <FontAwesomeIcon className="mr-2" icon={['fas', 'user']} />
                                    <b className="text-primary">{user.name}</b>
                                </div>
                                <div>
                                    <FontAwesomeIcon className="mr-2" icon={['fas', 'envelope']} />
                                    {user.email}
                                </div>
                                <div>
                                    <FontAwesomeIcon className="mr-2" icon="unlock" />
                                    <span className={statusClass}>{statusText}</span>
                                </div>
                            </div>
                        </div>
                    </CardBody>
                </Card>
                <Card className="mb-3">
                    <CardHeader>
                        Cập nhật mật khẩu
                </CardHeader>
                    <CardBody>
                        <div>
                            <div className="col-12 row mx-0 px-0 mb-3">
                                <div className="col-4 text-right pt-1 ">
                                    Mật khẩu cũ
                                    </div>
                                <div className="col-6 form-group mb-0">
                                    <input
                                        value={oldPassword}
                                        onChange={(event) => this.onChangeField('oldPassword', event.target.value)}
                                        type="password"
                                        placeholder="Nhập mật khẩu cũ"
                                        className="form-control" />
                                    {oldPasswordError && <small className="text-danger">
                                        {oldPasswordError}
                                    </small>}
                                </div>
                            </div>

                            <div className="col-12 row mx-0 px-0 mb-3">
                                <div className="col-4 text-right pt-1 ">
                                    Mật khẩu mới
                                    </div>
                                <div className="col-6 form-group mb-0">
                                    <input
                                        value={newPassword}
                                        type="password"
                                        placeholder="Nhập mật khẩu mới"
                                        onChange={(event) => this.onChangeField('newPassword', event.target.value)}
                                        className="form-control" />
                                    {newPasswordError && <small className="text-danger">
                                        {newPasswordError}
                                    </small>}
                                </div>
                            </div>
                            <div className="col-12 row mx-0 px-0 mb-3">
                                <div className="col-4 text-right pt-1 ">
                                    Nhập lại mật khẩu mới
                                    </div>
                                <div className="col-6 form-group mb-0">
                                    <input
                                        value={confirmPassword}
                                        placeholder="Nhập lại mật khẩu mới"
                                        type="password"
                                        onChange={(event) => this.onChangeField('confirmPassword', event.target.value)}
                                        className="form-control" />
                                    {confirmPasswordError && <small className="text-danger">
                                        {confirmPasswordError}
                                    </small>}
                                </div>
                            </div>

                            <div className="col-10 row justify-content-end mx-0">
                                <button onClick={() => this.changePassword()} className="btn btn-success">Cập nhật</button>
                            </div>
                        </div>
                    </CardBody>
                </Card>


                <Toast ref={ref => this.toast = ref} />
            </div >
        );
    }
}

export default Account;