import React from 'react';
import ReactTable from 'react-table';
import { Card, CardBody, CardHeader, Col, Row, Input } from 'reactstrap';
import { Nav } from 'react-bootstrap';
import Page from '../../components/Page';
import Toast from '../../components/Toast';
import api from '../../utils/api';

export default class Payment extends React.Component{
    columns = [
        {
          Header: () => (
            <span className='fa-task'>Ngày</span>
          ),
          accessor: 'createdAt',
          Cell: props => <div>{props.value}</div>
        },
        {
          Header: () => (
            <span className='fa-task'>Tên</span>
          ),
          accessor: 'user_id',
          Cell: props => <div>{props.value? props.value.name : ''}</div>
        },
        {
          Header: () => (
            <span className='fa-task'>Số tiền</span>
          ),
          accessor: 'value',
          Cell: props => <div>{props.value? props.value : ''}</div>
        },
        {
          Header: () => (
            <span className='fa-task'>Loại</span>
          ),
          accessor: 'type',
          Cell: props => <div>{props.value}</div>
        },
        {
          Header: () => (
            <span className='fa-task'>Số dư cũ</span>
          ),
          accessor: 'old_balance',
          Cell: props => <div>{props.value}</div>
        },
        {
          Header: () => (
            <span className='fa-task'>Số dư mới</span>
          ),
          accessor: 'new_balance',
          Cell: props => <div>{props.value}</div>
        },
        {
          Header: () => (
            <span className='fa-task'>Trạng thái</span>
          ),
          accessor: 'status',
          Cell: props => <div>{props.value}</div>
        }
      ] 

      constructor(props){
        super(props);
        this.state = {
          page: 0,
          pages: null,
          pageSize: 10,
          sort: '-createdAt',
          data: [],
          users: [],
          user: {name:'Người dùng', _id: ''}
        }
      }

      getData = (info) =>{
        let { page, pageSize, sorted } = info;
        let { type, user } = this.state;
        let sort = sorted ? sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString() : this.state.sort;
        api.getPayments({ limit: pageSize, page: page + 1, sort, type, user_id: user._id}).then(response => {
          if (response.success) {
            this.setState({ data: response.data, page, pageSize, sort, pages: response.totalPage });
          }
          else {
            this.toast.showMessage('đã xảy ra lỗi');
          }
        });
      }

      render() {
        let {page, pages, pageSize, sort, data} = this.state;
        return(
          <Page>
            <CardHeader>
                Danh sách Thanh toan
              </CardHeader>
          <Row>
            <Col>
              <Card className='mb-3'>
                <CardBody>
                  <ReactTable
                    className='overflow-hidden'
                    data={data}
                    columns={this.columns}
                    // defaultPageSize={20}
                    defaultPageSize={pageSize}
                    page={page}
                    pages={pages}
                    pageSize={pageSize}
  
                    onFetchData={this.getData}
                    showPageJump={true}
                    manual
                  // loading={isLoading}
                  />
                </CardBody>
              </Card>
            </Col>
          </Row>
          <Toast ref={(ref) => this.toast = ref} />
        </Page>
        )
      }
}