import Page from '../../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row } from 'reactstrap';
import 'react-table/react-table.css';
import { Link } from 'react-router-dom';
import queryParser from 'query-string';
import DateTime from 'react-datetime';
import utils from '../../utils/utils';
import _ from 'lodash';
import api from '../../utils/api';
import Toast from '../../components/Toast';
import Config from '../../utils/config';

class Reward extends React.Component {
  constructor(props) {
    super(props);

    const query = queryParser.parse(props.location.search);
    if (query.id) {
      this.getData(query.id);
    }

    this.state = {
      isLoading: !!query.id,
      id: query.id,
      name: '',
      nameError: null,
      amount: 1,
      amountError: null,
      remainAmount: 0,
      remainAmountError: null,
      point: 1000,
      pointError: null,
      amountPerUser: 1,
      amountPerUserError: null,
      startTime: Date.now(),
      startTimeError: null,
      endTime: Date.now() + 86400000,
      endTimeError: null,
      image: null,
      imageError: null,
      imageBase64: '',
      status: 'active'
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.location.search != this.props.location.search) {
      const query = queryParser.parse(nextProps.location.search);
      if (query.id != this.state.id && query.id) {
        this.getData(query.id);
        this.setState({ isLoading: true, id: query.id });
      }
    }
  }

  getData(id) {
    api.getRewardDetail(id).then(response => {
      if (response.success) {
        const data = response.data;
        this.setState({
          imageBase64: utils.getFullUrl(data.image),
          point: data.point,
          startTime: new Date(data.start_time).getTime(),
          endTime: new Date(data.end_time).getTime(),
          amountPerUser: data.limit_redeem,
          amount: data.total,
          name: data.name,
          remainAmount: data.remain,
          remainAmountError: null,
          status: data.status
        });
      } else {
        this.showMessage('Lấy thông tin quà tặng thất bại.');
      }
    });
  }

  onChangeField(key, name) {
    const errorKey = key + 'Error';
    this.setState({ [key]: name, [errorKey]: null });
  }

  changeImage(event) {
    utils
      .loadBase64OfFile(this.imageInput.files[0])
      .then(base64 => {
        this.setState({
          imageBase64: base64,
          image: this.imageInput.files[0],
          imageError: null
        });
      })
      .catch(err => {
        this.setState({
          imageBase64: '',
          image: null,
          imageError: 'Không thể hiển thị file.'
        });
      });
  }

  onSubmit() {
    const { name, startTime, endTime, image, id } = this.state;
    const point = parseInt(this.state.point);
    const amount = parseInt(this.state.amount);
    const amountPerUser = parseInt(this.state.amountPerUser);
    const remainAmount = parseInt(this.state.remainAmount);

    //reset error
    this.setState({
      nameError: null,
      amountError: null,
      amountPerUserError: null,
      startTimeError: null,
      endTimeError: null,
      imageError: null,
      pointError: null,
      remainAmountError: null
    });

    const error = {};

    if (!name || name.length <= 0) {
      error.nameError = 'Bạn phải nhập tên quà.';
    }

    if (_.isNaN(point) || point < 0) {
      error.pointError = 'Điểm không hợp lệ.';
    }

    if (_.isNaN(amount) || amount < 0) {
      error.amountError = 'Số lượng không hợp lệ.';
    }

    if (_.isNaN(amountPerUser) || amountPerUser < 0) {
      error.amountPerUserError = 'Số lượng không hợp lệ.';
    }

    if (startTime >= endTime) {
      error.endTimeError = 'Thời gian kết thúc phải lơn hơn thì gian bắt đầu.';
    }

    if (!image && !id) {
      error.imageError = 'Bạn phải chọn ảnh cho quà.';
    }

    if (id && remainAmount > amount) {
      error.remainAmountError = 'Số lượng còn lại phải nhỏ hơn tổng số.';
    }

    if (!_.isEmpty(error)) {
      this.setState(error);
      return;
    }

    if (!id) {
      api.createReward(this.state).then(response => {
        if (response.success) {
          this.setState({
            name: '',
            amount: 1,
            point: 1000,
            amountPerUser: 1,
            startTime: Date.now(),
            endTime: Date.now() + 86400000,
            image: null,
            base64: ''
          });
          this.showMessage('Thêm quà thành công');
        } else {
          this.showMessage('Thêm quà thất bại');
        }
      });
    } else {
      api.updateReward(this.state).then(response => {
        if (response.success) {
          this.showMessage('Cập nhật thành công');
        } else {
          this.showMessage('Cập nhật thất bại');
        }
      });
    }
  }

  showMessage(msg) {
    this.toast.showMessage(msg);
  }

  render() {
    const {
      name,
      amount,
      amountPerUser,
      startTime,
      endTime,
      point,
      image,
      nameError,
      amountError,
      amountPerUserError,
      startTimeError,
      endTimeError,
      pointError,
      imageError,
      imageBase64,
      id,
      remainAmount,
      remainAmountError,
      status
    } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'Thêm quà tặng', active: false }]} className='TablePost'>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>Thêm quà tặng</CardHeader>
              <CardBody>
                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Tên quà</div>
                  <div className='col-6 form-group mb-0'>
                    <input
                      value={name}
                      onChange={event => this.onChangeField('name', event.target.value)}
                      placeholder='Nhập tên quà'
                      className='form-control'
                    />
                    {nameError && <small className='text-danger'>{nameError}</small>}
                  </div>
                </div>

                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Tổng số lượng quà</div>
                  <div className='col-6 form-group mb-0'>
                    <input
                      value={amount}
                      type='number'
                      onChange={event => this.onChangeField('amount', event.target.value)}
                      className='form-control'
                    />
                    {amountError && <small className='text-danger'>{amountError}</small>}
                  </div>
                </div>
                {id && (
                  <div className='col-12 row mx-0 px-0 mb-3'>
                    <div className='col-4 text-right pt-1 '>Số quà còn lại</div>
                    <div className='col-6 form-group mb-0'>
                      <input
                        value={remainAmount}
                        type='number'
                        onChange={event => this.onChangeField('remainAmount', event.target.value)}
                        className='form-control'
                      />
                      {remainAmountError && <small className='text-danger'>{remainAmountError}</small>}
                    </div>
                  </div>
                )}

                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Điểm cần để đổi quà</div>
                  <div className='col-6 form-group mb-0'>
                    <input value={point} type='number' onChange={event => this.onChangeField('point', event.target.value)} className='form-control' />
                    {pointError && <small className='text-danger'>{pointError}</small>}
                  </div>
                </div>

                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Số quà tối đa 1 người được đổi</div>
                  <div className='col-6 form-group mb-0'>
                    <input
                      value={amountPerUser}
                      type='number'
                      onChange={event => this.onChangeField('amountPerUser', event.target.value)}
                      className='form-control'
                    />
                    {amountPerUserError && <small className='text-danger'>{amountPerUserError}</small>}
                  </div>
                </div>

                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Thời gian bắt đầu</div>
                  <div className='col-6 form-group mb-0'>
                    <DateTime
                      value={startTime}
                      dateFormat='DD/MM/YYYY'
                      timeFormat=' HH:mm:ss'
                      onChange={value => this.onChangeField('startTime', value)}
                    />
                    {startTimeError && <small className='text-danger'>{startTimeError}</small>}
                  </div>
                </div>
                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Thời gian kết thúc</div>
                  <div className='col-6 form-group mb-0'>
                    <DateTime
                      value={endTime}
                      dateFormat='DD/MM/YYYY'
                      timeFormat=' HH:mm:ss'
                      onChange={value => this.onChangeField('endTime', value)}
                    />
                    {endTimeError && <small className='text-danger'>{endTimeError}</small>}
                  </div>
                </div>
                {id && (
                  <div className='col-12 row mx-0 px-0 mb-3'>
                    <div className='col-4 text-right pt-1 '>Trạng thái</div>
                    <div className='col-6 form-group mb-0'>
                      <select value={status} onChange={event => this.onChangeField('status', event.target.value)}>
                        <option value='active'>Kích hoạt</option>
                        <option value='disable'>Vô hiệu hóa</option>
                      </select>
                    </div>
                  </div>
                )}
                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Ảnh quà</div>
                  <div className='col-8 form-group mb-0'>
                    <input
                      type='file'
                      className='mb-2'
                      accept='image/*'
                      ref={ref => (this.imageInput = ref)}
                      onChange={event => this.changeImage(event)}
                    />
                    <img width='200' height='200' src={imageBase64} />
                    {imageError && (
                      <div className='col-12 px-0 '>
                        {' '}
                        <small className='text-danger'>{imageError}</small>
                      </div>
                    )}
                  </div>
                </div>

                <div className='col-12 row mx-0 justify-content-center'>
                  <button onClick={() => this.onSubmit()} className='btn btn-success mx-2'>
                    {id ? 'Sửa' : 'Thêm'}
                  </button>
                  <Link to={Config.HOST_PREFIX + '/reward'}>
                    <button className='btn btn-danger mx-2'>Hủy</button>
                  </Link>
                </div>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Toast ref={ref => (this.toast = ref)} />
      </Page>
    );
  }
}

export default Reward;
