import React from 'react';
import ReactTable from 'react-table';
import api from '../../utils/api';
import Toast from '../../components/Toast';
import {Nav} from 'react-bootstrap';
import { Card, CardBody, CardHeader, Col, Row, Button } from 'reactstrap';
import Page from '../../components/Page';

export default class PromotionCode extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sort: '-createdAt',
      pages: null,
      page: 0,
      pageSize: 20,
      data: [],
      status: 'active'
    }
  }

  columns = [
    {
      Header: () => (
        <span className='fa-task'>Ngày lấy mã</span>
      ),
      accessor: 'createdAt',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Mã</span>
      ),
      accessor: 'code',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Doanh Nghiệp</span>
      ),
      accessor: 'promotion_id',
      Cell: props => <div>{props.value.company_id}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Ngày dùng</span>
      ),
      accessor: 'date_used',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Tong tieu dung</span>
      ),
      // accessor: '',
      Cell: <div>100000</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Khuyến mãi</span>
      ),
      accessor: 'promotion_id',
      Cell: props => <div>{props.value.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Hoàn tiền</span>
      ),
      accessor: 'promotion_id',
      Cell: props => <div>{props.value.refund}</div>
    },
  ]

  onFetchData(info) {
    let { page, pageSize, sorted } = info;
    let sort = sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString();
    api.getPromotionCode({ limit: pageSize, page: page + 1, sort }).then(response => {
      if (response.success) {
        let { data, totalPage } = response;
        this.setState({ data, pages: totalPage, pageSize, page, sort });
      }
      else {
        this.toast.showMessage('get promotion code failed');
      }
    })
  }

  selectCategory(event){
    let {page, pageSize, sort} = this.state;
    api.getPromotionCode({limit: pageSize, page: page + 1, sort, status: event}).then(response => {
      if(response.success) {
        let {data, totalPage} = response;
        this.setState({data, pages: totalPage, status: event});
      }
      else{
        this.toast.showMessage('get promotion code failed');
      }
    })
  }

  render() {
    let { pages, page, data, pageSize } = this.state
    return (
      <Page breadcrumbs={[{ name: 'Khuyen mai', active: true }]} className='TablePost'>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>
                Danh sách Khuyến mãi
                    </CardHeader>
              <Nav
                variant='tabs'
                defaultActiveKey={'active'}
                onSelect={e => {
                  this.selectCategory(e);
                }}
              >
                <Nav.Item>
                  <Nav.Link eventKey={'used'}>Đã dùng</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey={'active'}>Chưa dùng</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey={'expired'}>Hết hạn</Nav.Link>
                </Nav.Item>
              </Nav>
              <CardBody>
                <ReactTable
                  className='overflow-hidden'
                  columns={this.columns}
                  data={data}
                  pages={pages}
                  page={page}
                  defaultPageSize={pageSize}
                  pageSize={pageSize}
                  onFetchData={(info) => this.onFetchData(info)}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Toast ref={(ref) => this.toast = ref} />
      </Page>

    )
  }
}
