import React from 'react';
import {
    Card,
    CardBody,
    CardHeader,
    Col,
    Row,
    Table,
    Button,
} from 'reactstrap';
import { Link } from 'react-router-dom';
import _ from 'lodash';
import queryParser from 'query-string';
import api from '../utils/api';
import utils from '../utils/utils';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Toast from '../components/Toast';
import Config from '../utils/config';

const typeVi = {
    national_identity_card: "Chứng minh nhân dân",
    passport: 'Hộ chiếu',
    driving_license: 'Giấy phép lái xe'
}
export class UserDetail extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isFetching: true,
            user: {},
            images: [],
        }
        this.getData(props);
    }

    componentWillReceiveProps(nextProps) {
        this.getData(nextProps);
    }

    getData(props) {
        if (!this.state.isFetching || _.isEmpty(this.state.user)) {
            this.setState({ isFetching: true, user: {} });
        }

        // const query = queryParser.parse(props.location.search);
        if (this.props.id) {
            this.userId = this.props.id;
            api.getUserInfo(this.props.id).then(response => {
                if (response.success) {
                    this.setState({ user: response.data });

                    //Download KYC image
                    const user = response.data;
                    const passports = user.passport_image ? [...user.passport_image, {}, {}] : [{}, {}];
                    const selfie = user.selfy_image ? user.selfy_image : {};
                    const paths = [passports[0].path, passports[1].path, selfie.path];
                    paths.forEach(item => {
                        if (item) {
                            api.fetchRawData(utils.getFullUrl(item)).then(response => {
                                if (response.success) {
                                    const images = [...this.state.images, response.data];
                                    this.setState({ images });
                                }
                            })
                        }
                    });
                }
            });
        }
    }

    changeStatus(status) {
        const data = {
            id: this.userId,
            selfy_status: status,
            front_passport_status: status,
            backside_passport_status: status,
        };
        api.approveKYC(data).then(response => {
            if (response.success) {
                this.toast.showMessage('Đã thay đổi trạng thái KYC');
                this.setState({ user: { ...response.data } });
            } else {
                this.toast.showMessage('Thay đổi trạng thái KYC thái bại');
            }
        });
    }

    render() {
        const {
            user,
            images,
        } = this.state;

        let statusText = 'Đang chờ duyệt KYC';
        let statusClass = 'text-warning';
        if (user.status === 'approve-kyc') {
            statusText = 'Đã duyệt KYC';
            statusClass = 'text-success';
        } else if (user.status === 'reject-kyc') {
            statusText = 'Đã từ chối KYC';
            statusClass = 'text-danger';
        } else if (user.status === 'non-kyc') {
            statusText = 'Chưa summit KYC';
            statusClass = '';
        }

        return (
            <div className="col-12">
                <Card className="mb-3">
                    <CardHeader>
                        Thông tin thành viên
                </CardHeader>
                    <CardBody>
                        <div className="col-12 row">
                            <img
                                className="border rounded-circle"
                                src={utils.getAvatarOfUser(user.avatar)} width="150" height="150" />
                            <div className="px-3 text-muted">
                                <div>
                                    <FontAwesomeIcon className="mr-2" icon={['fas', 'user']} />
                                    <b className="text-primary">{user.name}</b>
                                </div>
                                <div>
                                    <FontAwesomeIcon className="mr-2" icon={['fas', 'envelope']} />
                                    {user.email}
                                </div>
                                <div>
                                    <FontAwesomeIcon className="mr-2" icon={['fas', 'calendar-alt']} />
                                    {utils.convertTimeToYYYYMMDDString(user.createdAt) + ' ' + utils.convertTimeToHHMMString(user.createdAt)}
                                    (ngày tham gia)
                                </div>
                                <div>
                                    <FontAwesomeIcon className="mr-2" icon="coins" />
                                    {utils.getHumanReadableNumber(user.point)}
                                </div>
                                <div>
                                    <FontAwesomeIcon className="mr-2" icon={['fas', 'code-branch']} />
                                    {user.ref_code}
                                </div>
                                <div>
                                    <FontAwesomeIcon className="mr-2" icon="unlock" />
                                    <span className={statusClass}>{statusText}</span>
                                </div>
                            </div>
                        </div>
                    </CardBody>
                </Card>

                {user.status !== 'non-kyc' && <Card>
                    <CardHeader>
                        KYC
                </CardHeader>
                    <CardBody>
                        <div className="col-12 mb-3">
                            <div>
                                <FontAwesomeIcon className="mr-2" icon={['fas', 'user']} />
                                {user.real_name}
                            </div>
                            <div>
                                <FontAwesomeIcon className="mr-2" icon={['fas', 'calendar-alt']} />
                                {user.birthday}
                            </div>
                            <div>
                                <FontAwesomeIcon className="mr-2" icon="address-card" />
                                {user.address}
                            </div>
                            <div>
                                <FontAwesomeIcon className="mr-2" icon={['fas', 'address-book']} />
                                {typeVi[user.passport_type]}
                            </div>
                        </div>

                        <div className="col-12 row mx-0">
                            <div className="col-4">
                                <img className="w-100"
                                    src={images[0]} />
                            </div>
                            <div className="col-4">
                                <img className="w-100"
                                    src={images[1]} />
                            </div>
                            <div className="col-4">
                                <img className="w-100"
                                    src={images[2]} />
                            </div>
                        </div>
                        <div className="col-12 row mx-0 justify-content-center">
                            <button onClick={() => this.changeStatus('approve')}
                                className="btn btn-success mx-2 my-2">Duyệt</button>
                            <button onClick={() => this.changeStatus('reject')}
                                className="btn btn-danger mx-2 my-2">Từ chối</button>
                        </div>
                    </CardBody>
                </Card>}
                <Toast ref={ref => this.toast = ref} />
            </div>
        );
    }
}

export default UserDetail;