import config from './config';

let fbImageType = 'normal';
let youtubeImageType = 'hqdefault';
fbImageType = 'large';
// youtubeImageType = 'maxresdefault';

function getFacebookAvatar(fbId) {
    return `http://graph.facebook.com/${fbId}/picture?type=${fbImageType}`
}

function getYoutubeThumbnail(id) {
    return `https://img.youtube.com/vi/${id}/${youtubeImageType}.jpg`;
}

function getAvatarOfUser(avatar, fb_id) {
    if (avatar) {
        if (!avatar.includes('http')) {
            avatar = config.IMAGE_HOST + '/' + avatar;
        }
        return avatar;
    } else if (fb_id) {
        return getFacebookAvatar(fb_id);
    }

    return null;
}

function getFullUrl(url) {
    if (url) {
        if (!url.includes('http')) {
            url = config.IMAGE_HOST + '/' + url;
        }
        return url;
    }

    return null;
}

function shuffle(array) {
    if (!array) {
        return [];
    }
    var currentIndex = array.length, temporaryValue, randomIndex;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {

        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;

        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
    }

    return array;
}

const convertTimeToYYYYMMDDString = (time) => {
    const date = new Date(time);
    const year = date.getFullYear();
    const mon = ('0' + (date.getMonth() + 1)).slice(-2);
    const day = ('0' + (date.getDate())).slice(-2);
    return day + '/' + mon + '/' + year;
};

const convertTimeToHHMMString = (time) => {
    const date = new Date(time);
    const hour = ('0' + date.getHours()).slice(-2);
    const min = ('0' + date.getMinutes()).slice(-2);
    return hour + ':' + min;
};

const upperCaseFirstLetter = string => {
    if (!string || string.length == 0) {
        return string;
    }

    return string.charAt(0).toUpperCase() + string.slice(1);
}

function getHumanReadableNumber(number) {
    const postfix = ["K", "M", "B", "T"];
    const arrange = [1000, 1000000, 1000000000, 1000000000000];
    for (let i = arrange.length - 1; i >= 0; i--) {
        if (number > arrange[i]) {
            number = (number / arrange[i]).toFixed(2) + postfix[i];
            break;
        }
    }

    return number;
}

function loadBase64OfFile(path) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader;
        reader.onerror = reject;
        reader.onload = () => {
            resolve(reader.result);
        };
        reader.readAsDataURL(path);
    });
}

function getStartTimeOfDay(dayAgo) {
    const date = new Date(Date.now() - dayAgo * 86400000);
    date.setHours(0);
    date.setMinutes(0);
    date.setMilliseconds(0);
    date.setSeconds(0);
    return date.getTime();
}


export default {
    getFacebookAvatar,
    getYoutubeThumbnail,
    shuffle,
    convertTimeToHHMMString,
    convertTimeToYYYYMMDDString,
    upperCaseFirstLetter,
    getHumanReadableNumber,
    getAvatarOfUser,
    getFullUrl,
    loadBase64OfFile,
    getStartTimeOfDay
}