import Page from '../../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table, Button } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import cellEditFactory from 'react-bootstrap-table2-editor';
import { Link } from 'react-router-dom';

import Api from '../../utils/api';
import Utils from '../../utils/utils';
import queryParser from 'query-string';
import Config from '../../utils/config';

class Category extends React.Component {
  columns = [
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tên thể loại
        </span>
      ),
      accessor: 'name',
      Cell: props => (
        <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>
          <b>{props.value}</b>
        </div>
      )
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Trạng thái
        </span>
      ),
      width: 250,
      accessor: 'status',
      Cell: props => {
        let text = 'Kích hoạt';
        let color = 'text-success';
        if (props.value === 'disable') {
          text = 'Không kích hoạt';
          color = 'text-danger';
        }

        return <span className={color}>{text}</span>;
      }
    },
    {
      Header: 'Thao tác',
      accessor: '_id',
      Cell: props => (
        <div className='action-button'>
          <div className='mx-2'>
            <Button onClick={() => this.updateCategory(props.value)} color='warning'>
              Sửa
            </Button>
          </div>
          <div className='mx-2'>
            <Button onClick={() => this.updateCategory(props.value, 'active')} color='success'>
              Kích hoạt
            </Button>
          </div>
          <div className='mx-2'>
            <Button onClick={() => this.updateCategory(props.value, 'disable')} color='danger'>
              Vô hiệu
            </Button>
          </div>
        </div>
      )
    }
  ];

  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      name: '',
      status: 'default',
      id: null
    };

    this.getData();
  }

  componentWillReceiveProps(nextProps) {
    this.getData();
  }

  getData() {
    this.setState({
      isLoading: true,
      data: []
    });

    Api.getCategory().then(response => {
      if (response.success) {
        const data = response.data.map(item => {
          return item;
        });

        this.setState({
          isLoading: false,
          data
        });
      } else {
        this.setState({
          isLoading: false,
          error: 'Tải dữ liệu lỗi.'
        });
      }
    });
  }

  onChangeField(key, name) {
    this.setState({ [key]: name });
  }

  updateCategory(id, status) {
    const item = this.state.data.find(item => item._id === id);
    if (!item) {
      return;
    }

    if (status) {
      this.updateCategoryOnServer(id, item.name, status);
    } else {
      this.setState({
        id: item._id,
        name: item.name,
        status: item.status
      });
    }
  }

  updateCategoryOnServer(id, name, status) {
    Api.updateCategory({ id, name, status }).then(response => {
      if (response.success) {
        this.setState({
          isLoading: false,
          data: this.state.data.map(item => {
            if (item._id == id) {
              item.name = name;
              item.status = status;
            }
            return item;
          }),
          name: '',
          status: 'default',
          id: null
        });
      } else {
        this.setState({ isLoading: false, error: 'Sửa thể loại thất bại' });
      }
    });
  }

  onSubmit() {
    const { id, name, status } = this.state;
    this.setState({ isLoading: true, error: null });
    if (id) {
      this.updateCategoryOnServer(id, name, status);
    } else {
      Api.createCategory({ name, status }).then(response => {
        if (response.success) {
          this.setState({
            isLoading: false,
            data: [...this.state.data, response.data],
            name: '',
            status: 'default',
            id: null
          });
        } else {
          this.setState({ isLoading: false, error: 'Thêm thể loại thất bại' });
        }
      });
    }
  }

  getType(query) {
    const parsed = queryParser.parse(query);
    return parsed.type;
  }

  render() {
    const { isLoading, data, name, status, id } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'tables', active: true }]} className='TablePost'>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>Thể loại</CardHeader>
              <CardBody>
                <div className='form-inline justify-content-end mb-3'>
                  <label>{id ? 'Sửa thể loại:' : 'Thêm thể loại:'}</label>
                  <div className='form-group mx-3'>
                    <input
                      className='form-control'
                      value={name}
                      onChange={event => this.onChangeField('name', event.target.value)}
                      placeholder='Nhập tên'
                    />
                  </div>
                  <div className='form-group mx-3'>
                    <select className='form-control' value={status} onChange={event => this.onChangeField('status', event.target.value)}>
                      <option value='default' disabled>
                        Trạng thái
                      </option>
                      <option value='active'>Kích hoạt</option>
                      <option value='disable'>Không kích hoạt</option>
                    </select>
                  </div>
                  <button
                    onClick={() => this.onSubmit()}
                    disabled={name === '' || status === 'default' || isLoading}
                    className='btn btn-success mx-3'
                  >
                    {id ? 'Sửa' : 'Thêm'}
                  </button>

                  {id && (
                    <button onClick={() => this.setState({ id: null, name: '', status: 'default' })} className='btn btn-dark mx-3'>
                      Hủy
                    </button>
                  )}
                </div>
                <ReactTable data={data} loading={isLoading} columns={this.columns} defaultPageSize={15} />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Page>
    );
  }
}

export default Category;
