import Page from '../../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Input } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import Api from '../../utils/api';
import classnames from 'classnames';
import Notification from '../../components/Toast';
import MModal from '../../components/MModal';
import Store from '../companies/Store';
import Staff from '../employees/Staff';
import Moment from '../../components/Moment';

class TableOrder extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      error: null,
      pages: null,
      page: 0,
      pageSize: 10,
      columns: this.defaultColumns,
      status: '',
      showModal: false,
      modal: '',
      company_id: ''
    };
  }

  getData = state => {
    const sort = state.sorted && state.sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString();
    this.setState({
      isLoading: true,
      page: state.page,
      pageSize: state.pageSize
    });
    Api.getOrders({
      status: state.status || this.state.status,
      page: state.page + 1,
      limit: state.pageSize,
      sort
      // select: 'images name total_store total_staff total_revenue status'
    }).then(response => {
      if (response.success) {
        this.setState({
          isLoading: false,
          pages: response.total_page,
          data: response.data
        });
      } else {
        this.setState({
          isLoading: false,
          error: 'Tải dữ liệu lỗi.'
        });
      }
    });
  };

  getStatus = value => {
    switch (value) {
      case 'pending':
        return 'Đang chờ';
      case 'handling':
        return 'Đang xử lý';
      case 'completed':
        return 'Đã hoàn thành';
      case 'user_canceled':
        return 'Người dùng hủy';
      case 'company_canceled':
        return 'Doanh nghiệp hủy';
      case 'user_rejected':
        return 'Người dùng từ chối';
      case 'company_rejected':
        return 'Doanh nghiệp từ chối';
    }
  };
  defaultColumns = [
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Ngày
        </span>
      ),
      width: 150,
      accessor: 'createdAt',
      Cell: props => <Moment date={props.value} />,
      maxWidth: 200
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Mã đơn hàng
        </span>
      ),
      width: 150,
      accessor: 'code',
      Cell: ({ value }) => {
        return <b title={value}>{value}</b>;
      }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tên người dùng
        </span>
      ),
      sortable: false,
      width: 200,
      accessor: 'user.name',
      Cell: props => {
        return <b>{props.value}</b>;
      }
    },
    {
      Header: 'Điện thoại',
      accessor: 'phone_number',
      sortable: false,
      width: 150,
      Cell: props => {
        return <b>{props.value}</b>;
      }
    },
    {
      Header: 'Cửa hàng',
      accessor: d => [d['store'], d['company']],
      id: 'cpn',
      className: 'd-block overflow-hidden',
      sortable: false,
      Cell: ({ value }) => {
        return (
          <b
            style={{ display: 'block', overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}
            title={value[0].name + '-' + value[1].name}
          >
            {value[0].name + '-' + value[1].name}
          </b>
        );
      }
    },
    {
      Header: 'Nhân viên',
      maxWidth: 100,
      accessor: 'staff',
      sortable: false,
      Cell: ({ value }) => {
        return value ? value : 'null';
      }
    },
    {
      Header: 'Tổng tiền',
      maxWidth: 125,
      style: { display: 'flex', justifyContent: 'center' },
      accessor: 'total',
      Cell: ({ value }) => value
    },
    {
      Header: 'Khuyến mãi',
      maxWidth: 125,
      style: { display: 'flex', justifyContent: 'center' },
      accessor: 'total_discount',
      Cell: ({ value }) => value
    },
    {
      Header: 'Hoàn tiền',
      maxWidth: 125,
      style: { display: 'flex', justifyContent: 'center' },
      accessor: 'total_refund',
      Cell: ({ value }) => value
    },
    {
      Header: 'Trạng thái',
      maxWidth: 200,
      style: { display: 'flex', justifyContent: 'center' },
      accessor: 'status',
      Cell: ({ value }) => {
        return (
          <Input
            disabled
            style={{ width: '100%' }}
            value={this.getStatus(value)}
            className={classnames({
              'bg-warning': value === 'pending',
              'bg-primary': value === 'handling',
              'bg-success': value === 'completed',
              'bg-danger': value === 'user_canceled' || value === 'company_canceled',
              'bg-dark': value === 'user_rejected' || value === 'company_rejected',
              'text-white': value !== 'pending'
            })}
          />
        );
      },
      filterable: true,
      Filter: ({ filter, onChange }) => (
        <select
          onChange={event => {
            this.setState({ status: event.target.value });
            onChange();
          }}
          style={{ width: '100%' }}
          value={filter ? filter.value : ''}
        >
          <option value=''>Tất cả</option>
          {['pending', 'handling', 'completed', 'user_canceled', 'company_canceled', 'user_rejected', 'company_rejected'].map(item => (
            <option value={item} key={item}>
              {this.getStatus(item)}
            </option>
          ))}
        </select>
      )
    }
  ];
  onClose = () => {
    this.setState({
      showModal: false
    });
  };
  render() {
    const { data, isLoading, columns, pages, page, pageSize, modal, company_id } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'Company', active: true }]} className='TableOrder'>
        <Notification ref={notificationSystem => (this.notificationSystem = notificationSystem)} />
        <MModal showModal={this.state.showModal} onClose={this.onClose} title={modal.toUpperCase()}>
          {modal === 'store' && <Store company_id={company_id} />}
          {modal === 'staff' && <Staff company_id={company_id} />}
        </MModal>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>Danh sách đơn hàng</CardHeader>
              <Row />
              <CardBody>
                <ReactTable
                  loading={isLoading}
                  manual
                  onFetchData={this.getData}
                  data={data}
                  columns={columns}
                  defaultPageSize={10}
                  pages={pages}
                  pageSize={pageSize}
                  page={page}
                  resizable={false}
                  showPageJump={true}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Page>
    );
  }
}

export default TableOrder;
