import React, { Component } from 'react';
import PropTypes from 'prop-types';
import api from '../utils/api';
import utils from '../utils/utils';

export default class ImageAsync extends Component {
  constructor(props) {
    super(props);
    this.state = {
      image: ''
    };
  }
  componentDidMount() {
    api.fetchRawData(utils.getFullUrl(this.props.path)).then(res => {
      if (res.success) this.setState({ image: res.data });
    });
  }
  componentWillReceiveProps(nextProps) {
    console.log({ nextProps, props: this.props });
    if (nextProps.path !== this.props.path) {
      api
        .fetchRawData(utils.getFullUrl(nextProps.path))
        .then(res => {
          if (res.success) this.setState({ image: res.data });
          else this.setState({ image: '' });
        })
        .catch(err => this.setState({ image: '' }));
    }
  }

  static propTypes = {
    path: PropTypes.string.isRequired
  };

  render() {
    return this.state.image ? <img src={this.state.image} height='50px' width='200px' style={{ objectFit: 'cover' }} /> : <>error</>;
  }
}
