import Page from '../../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table, Button } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import { Link } from 'react-router-dom';
import Api from '../../utils/api';
import Utils from '../../utils/utils';
import Toast from '../../components/Toast';
import Config from '../../utils/config';

class Transaction extends React.Component {
  columns = [
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Ảnh đại diện
        </span>
      ),
      accessor: 'user_id.avatar',
      width: 100,
      Cell: props => {
        return (
          <img
            className='border rounded-circle'
            width={56}
            height={56}
            src={Utils.getAvatarOfUser(props.value) || require('../../assets/img/default_user_avatar.png')}
          />
        );
      }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tên
        </span>
      ),
      accessor: 'user_id',
      width: 150,
      Cell: props => {
        return (
          <div>
            <Link to={Config.HOST_PREFIX + '/admin/user-detail?id=' + (props.value? props.value._id : '')}>
              <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>
                <b>{props.value? props.value.name : ''}</b>
              </div>
            </Link>
          </div>
        );
      }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Ảnh
        </span>
      ),
      accessor: 'reward_id.image',
      width: 100,
      sortable: false,
      Cell: props => {
        return <img className='border ' width={56} height={56} src={Utils.getFullUrl(props.value)} />;
      }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tên quà tặng
        </span>
      ),
      sortable: false,
      accessor: 'reward_id.name',
      Cell: props => (
        <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>
          <b>{props.value}</b>
        </div>
      )
    },
    {
      Header: 'Thời gian',
      accessor: 'createdAt',
      width: 100,
      Cell: props => (
        <div>
          <div>{Utils.convertTimeToYYYYMMDDString(props.value)}</div>
          <div>{Utils.convertTimeToHHMMString(props.value)}</div>
        </div>
      )
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Trạng thái
        </span>
      ),
      width: 150,
      accessor: 'status',
      Cell: props => {
        let text = 'Đã duyệt';
        let color = 'text-success';
        if (props.value === 'rejected') {
          text = 'Từ chối';
          color = 'text-danger';
        } else if (props.value == 'pending') {
          text = 'Đợi duyệt';
          color = 'text-muted';
        }

        return <span className={color}>{text}</span>;
      }
    },
    {
      Header: 'Thao tác',
      accessor: 'showAction',
      maxWidth: 150,
      Cell: props => (
        <div className='row mx-0 justify-content-end'>
          {props.value.isShow && (
            <div className='mx-1 my-1'>
              <Button onClick={() => this.changeTransactionStatus(props.value._id, 'approved')} color='success' size='sm'>
                Chấp nhận
              </Button>
            </div>
          )}
          {props.value.isShow && (
            <div className='mx-1 my-1'>
              <Button onClick={() => this.changeTransactionStatus(props.value._id, 'rejected')} color='danger' size='sm'>
                Từ chối
              </Button>
            </div>
          )}
        </div>
      )
    }
  ];

  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      page: 0,
      pages: null,
      pageSize: 10
    };
  }

  getData = (state, instance) => {
    this.setState({
      isLoading: true,
      page: state.page,
      pageSize: state.pageSize,
      data: []
    });
    const sort = state.sorted && state.sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString();
    Api.getTransactions(state.pageSize, state.page + 1, sort).then(response => {
      if (response.success) {
        const data = response.data;
        this.setState({
          isLoading: false,
          data,
          pages: response.total_page
        });
      } else {
        this.setState({
          isLoading: false,
          error: 'Tải dữ liệu lỗi.'
        });
      }
    });
  };

  onChangeField(key, name) {
    this.setState({ [key]: name });
  }

  changeTransactionStatus(id, status) {
    const item = this.state.data.find(item => item._id === id);
    if (!item) {
      return;
    }

    Api.changeTransactionStatus(id, status).then(response => {
      if (response.success) {
        this.setState({
          isLoading: false,
          data: this.state.data.map(item => {
            if (item._id == id) {
              item.status = status;
            }
            return item;
          })
        });
      } else {
        this.showMessage('Sửa quà tặng thất bại');
      }
    });
  }

  showMessage(msg) {
    this.toast.showMessage(msg);
  }

  render() {
    const { isLoading, data, page, pageSize, pages } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'tables', active: true }]} className='TablePost'>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>Danh sách đổi quà</CardHeader>
              <CardBody>
                <ReactTable
                  data={data}
                  resolveData={data =>
                    data.map(item => {
                      item.amount = { remain: item.remain, total: item.total };
                      item.showAction = { _id: item._id, isShow: item.status === 'pending' };
                      return item;
                    })
                  }
                  manual
                  onFetchData={this.getData}
                  loading={isLoading}
                  columns={this.columns}
                  defaultPageSize={10}
                  page={page}
                  pageSize={pageSize}
                  pages={pages}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Toast ref={ref => (this.toast = ref)} />
      </Page>
    );
  }
}

export default Transaction;
