import logo200Image from '../assets/img/logo/logo.png';
import PropTypes from 'prop-types';
import React from 'react';
import { Form, FormGroup, Input, Label } from 'reactstrap';
import Api from '../utils/api';
import { Redirect } from 'react-router-dom';
import stateManager from '../stateManager/stateManager';
import Config from '../utils/config';
class AuthForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      user_name: '',
      password: '',
      gotoDashboard: false
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  get isLogin() {
    return this.props.authState === STATE_LOGIN;
  }

  get isSignup() {
    return this.props.authState === STATE_SIGNUP;
  }

  changeAuthState = authState => event => {
    event.preventDefault();

    this.props.onChangeAuthState(authState);
  };

  handleChange(key, event) {
    this.setState({ [key]: event.target.value });
  }

  handleSubmit = event => {
    let data = {
      user_name: this.state.user_name,
      password: this.state.password
    }
    Api.login(data)
      .then(response => {
        if (response.success) {
          Api.setToken(response.data.access_token);

          const admin = { access_token: response.data.access_token,...response.data.admin, expired: Date.now() + 1200000 };
          // localStorage.setItem('admin', JSON.stringify(admin));
          stateManager.notice({ isLogin: true, admin });
          this.setState({ gotoDashboard: true });
        }
        else {
          alert('Sai tên đăng nhập hoặc mật khẩu');
        }
      })
    event.preventDefault();
  };

  renderButtonText() {
    const { buttonText } = this.props;

    if (!buttonText && this.isLogin) {
      return 'Login';
    }

    if (!buttonText && this.isSignup) {
      return 'Signup';
    }

    return buttonText;
  }

  render() {
    if (this.state.gotoDashboard) {
      return <Redirect to={Config.HOST_PREFIX} />
    }
    const {
      showLogo,
      usernameLabel,
      usernameInputProps,
      passwordLabel,
      passwordInputProps,
      confirmPasswordLabel,
      confirmPasswordInputProps,
      children,
      onLogoClick,
    } = this.props;

    return (
      <Form onSubmit={this.handleSubmit}>
        {showLogo && (
          <div className="text-center pb-4">
            <img
              src={logo200Image}
              className="rounded"
              style={{ width: 60, height: 60, cursor: 'pointer' }}
              alt="logo"
              onClick={onLogoClick}
            />
          </div>
        )}
        <FormGroup>
          <Label for={usernameLabel}>{usernameLabel}</Label>
          <Input {...usernameInputProps} value={this.state.user_name} onChange={(event) => this.handleChange('user_name', event)} />
        </FormGroup>
        <FormGroup>
          <Label for={passwordLabel}>{passwordLabel}</Label>
          <Input {...passwordInputProps} value={this.state.password} onChange={(event) => this.handleChange('password', event)} />
        </FormGroup>
        {this.isSignup && (
          <FormGroup>
            <Label for={confirmPasswordLabel}>{confirmPasswordLabel}</Label>
            <Input {...confirmPasswordInputProps} />
          </FormGroup>
        )}
        <hr />
        <Input
          type="submit"
          value={this.renderButtonText()}
          bsSize="lg"
          className="bg-gradient-theme-left border-0 text-white"
          block
          onClick={this.handleSubmit}>

        </Input>

        <div className="text-center pt-1">
        </div>

        {children}
      </Form>
    );
  }
}

export const STATE_LOGIN = 'LOGIN';
export const STATE_SIGNUP = 'SIGNUP';

AuthForm.propTypes = {
  authState: PropTypes.oneOf([STATE_LOGIN, STATE_SIGNUP]).isRequired,
  showLogo: PropTypes.bool,
  usernameLabel: PropTypes.string,
  usernameInputProps: PropTypes.object,
  passwordLabel: PropTypes.string,
  passwordInputProps: PropTypes.object,
  confirmPasswordLabel: PropTypes.string,
  confirmPasswordInputProps: PropTypes.object,
  onLogoClick: PropTypes.func,
};

AuthForm.defaultProps = {
  authState: 'LOGIN',
  showLogo: true,
  usernameLabel: 'User name',
  usernameInputProps: {
    type: 'text',
    name: 'user_name',
    placeholder: 'user name',
  },
  passwordLabel: 'Password',
  passwordInputProps: {
    type: 'password',
    name: 'password',
    placeholder: 'your password',
  },
  confirmPasswordLabel: 'Confirm Password',
  confirmPasswordInputProps: {
    type: 'password',
    placeholder: 'confirm your password',
  },
  onLogoClick: () => { },
};

export default AuthForm;
