import React from 'react';
import {Dropdown} from 'react-bootstrap'

export default class DropDown extends React.Component{
    constructor(props){
        super(props);
        let data = this.props.data || [];
        let variant = this.props.variant || "success";
        let defaultSelected = this.props.defaultSelected || 0;
        let notShow = this.props.notShow || '';
        this.state = {
            data,
            variant,
            defaultSelected,
            notShow
        }
    }

    componentWillReceiveProps(nextProps){
        let {data , defaultSelected, variant} = this.state;
        if(nextProps.data.length !== data){
            data = nextProps.data;
        }
        if(nextProps.defaultSelected >= 0 && nextProps.defaultSelected !== defaultSelected){
            defaultSelected = nextProps.defaultSelected;
        }
        if(nextProps.variant !== variant) variant = nextProps.variant;
        this.setState({data, defaultSelected, variant});

    }

    select(index, id){
        if(this.props.onChange){
            this.props.onChange(index, id);
        }
        if(index!== this.state.defaultSelected){
            this.setState({defaultSelected: index});
        }

    }

    render(){
        let state = this.state;
        let data = state.data;
        let defaultSelected = state.defaultSelected;
        let defaultRender = data[defaultSelected] ? data[defaultSelected].name : '';
        let subDropDown = [];
        data.forEach((sub,index)=>{
            if(sub._id === state.notShow) return;
            if(index !== defaultSelected)
                subDropDown.push([
                    <Dropdown.Item
                        key={index}
                        onClick={()=>this.select(index, sub._id)}
                    >
                        {sub.name}
                    </Dropdown.Item>
                ]);
        });
                                            
        return(
            <Dropdown >
                <Dropdown.Toggle className="button-style" variant={this.state.variant}>
                    {defaultRender}
                </Dropdown.Toggle>
                <Dropdown.Menu>
                    {subDropDown}
                </Dropdown.Menu>
            </Dropdown>
        )
    }

}