import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class ListGroup extends React.Component{
    constructor(props){
        super(props);
    }

    select(index){
        if(this.props.onChange){
            this.props.onChange(index);
        }
    }

    render(){
        const groups = this.props.data.map((item,i)=>{
            let bgColor = 'white';
            if(i == this.props.activeIndex){
                bgColor = 'bg-info text-white ';
            }
            let borderTop = '';
            if(i === 0){
                borderTop = ' border-top';
            }

            return (
            <li style = {{
                cursor: 'pointer',
                color: 'black',
            }} 
            key={i}
            onClick={() => this.select(i)} 
            className={"border-bottom py-2 px-2  " + bgColor  + borderTop}>
                <FontAwesomeIcon icon="angle-right" className="mr-1   "/>{item.name}
            </li>);
        });

        return (
            <ul className="px-0">
                {groups}
            </ul>
        )
    }
}

export default ListGroup;