import React from 'react';
import api from '../../utils/api';
import ListConfig from './ListConfig';
import Toast from '../../components/Toast';
import Config from '../../utils/config';

export default class ConfigPage extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            listConfigs:[],
            indexEdit: -1,
            valueInput: ''
        }
        api.getListConfig().then(response => {
            if(response.success){
                this.setState({listConfigs: response.data});
            }
        })
    }

    selectToEdit(index, data, action, value){
        if(index !== this.state.indexEdit){
            let indexEdit = index;
            this.setState({indexEdit, valueInput: data.value});
        }
        if(action === 'save'){
            let dataUpdate = {
                key : data.key,
                value : value
            }
            api.updateConfig(dataUpdate).then(response => {
                if(response.success){
                    this.toast.showMessage("update config success");
                    let listConfigs = this.state.listConfigs;
                    listConfigs[index] = response.data;
                    this.setState({listConfigs, indexEdit: -1})
                }
                else{
                    this.toast.showMessage("update config fail")
                }
            })
        }
    }

    render(){
        let indexEdit = this.state.indexEdit;
        return(
            <div className="px-4">
                <ListConfig
                    valueInput = {this.state.valueInput}
                    indexEdit={indexEdit}
                    data={this.state.listConfigs}
                    onChange={(index, data,action, value)=>this.selectToEdit(index, data,action, value)}
                />
                <Toast
                    ref={(ref) => this.toast=ref}
                />
            </div>
            
        );
    }
}