import Page from '../../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Input } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import Api from '../../utils/api';
import Notification from '../../components/Toast';
import classnames from 'classnames';

class Product extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      error: null,
      pages: null,
      page: 0,
      pageSize: 20,
      columns: this.defaultColumns,
      status: ''
    };
  }

  getData = state => {
    const sort = state.sorted && state.sorted.length ? state.sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString() : '-updatedAt';
    this.setState({
      isLoading: true,
      data: [],
      pages: state.pages,
      page: state.page,
      pageSize: state.pageSize
    });
    Api.getProducts({
      status: state.status || this.state.status,
      page: state.page + 1,
      limit: state.pageSize,
      sort
    })
      .then(response => {
        if (response.success) {
          this.setState({
            isLoading: false,
            pages: response.total_page,
            data: response.data
          });
        } else {
          this.notificationSystem.showMessage('failed');
          this.setState({
            isLoading: false,
            error: 'Tải dữ liệu lỗi.'
          });
        }
      })
      .catch(err => this.notificationSystem.showMessage('failed'));
  };

  onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
    // this.getData({ ...this.state, status: e.target.value });
  };

  onApproveProduct = (id, status) => {
    Api.approveProduct({ id, status })
      .then(res => {
        if (res.success) {
          const index = this.state.data.findIndex(el => el._id === id);
          const newData = [...this.state.data];
          if (index >= 0) {
            newData[index] = res.data;
            this.setState({ data: newData });
          }
          this.notificationSystem.showMessage('Cập nhật thành công!');
        } else this.notificationSystem.showMessage('failed');
      })
      .catch(err => this.notificationSystem.showMessage('failed'));
  };

  defaultColumns = [
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tên SP
        </span>
      ),
      accessor: d => [d.name, d._id],
      id: 'name',
      style: { overflow: 'visible', whiteSpace: 'normal', fontWeight: 'bold' },
      Cell: ({ value }) => {
        return value[0];
      }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Mô tả
        </span>
      ),
      sortable: false,
      accessor: 'description',
      Cell: ({ value }) => value
    },
    {
      Header: 'Giá bán lẻ',
      accessor: 'price',
      width: 100,
      Cell: ({ value }) => {
        return value.toLocaleString('it-IT', { style: 'currency', currency: 'VND' });
      }
    },
    {
      Header: 'Hoàn tiền',
      maxWidth: 100,
      accessor: 'refund_rate',
      Cell: ({ value }) => value * 100 + '%'
    },
    {
      Header: 'Trạng thái',
      maxWidth: 125,
      accessor: 'status',
      Cell: props => (
        <Input
          className={classnames({
            'bg-success': props.value === 'approved',
            'bg-warning': props.value === 'pending',
            'bg-dark': props.value === 'disabled',
            'bg-danger': props.value === 'rejected',
            'text-white': props.value !== 'pending'
          })}
          type='select'
          name='status'
          id='exampleSelect'
          value={props.value}
          onChange={e => this.onApproveProduct(props.original._id, e.target.value)}
        >
          <option value='approved'>Kích hoạt</option>
          <option disabled hidden value='pending'>
            Đang chờ
          </option>
          <option value='rejected'>Từ chối</option>
          <option value='disabled'>Vô hiệu</option>
        </Input>
      ),
      filterable: true,
      Filter: ({ filter, onChange }) => {
        return (
          <select
            onChange={event => {
              this.setState({ status: event.target.value });
              onChange();
            }}
            style={{ width: '100%' }}
            value={filter ? filter.value : ''}
          >
            <option value=''>Tất cả</option>
            <option value='approved'>Đã duyệt</option>
            <option value='rejected'>Đã từ chối</option>
            <option value='pending'>Đang chờ</option>
            <option value='disabled'>Vô hiệu</option>
          </select>
        );
      }
    }
    // {
    //   Header: 'Hoạt động',
    //   maxWidth: 125,
    //   id: 'act',
    //   style: { display: 'flex', justifyContent: 'center' },
    //   sortable: false,
    //   accessor: d => [d._id, d.status],

    // }
  ];

  render() {
    const { data, isLoading, columns, pages, page, pageSize } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'Company', active: true }]} className='Product'>
        <Notification ref={notificationSystem => (this.notificationSystem = notificationSystem)} />
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>Danh sách sản phẩm</CardHeader>
              <CardBody>
                <ReactTable
                  loading={isLoading}
                  manual
                  onFetchData={this.getData}
                  data={data}
                  columns={columns}
                  defaultPageSize={20}
                  pages={pages}
                  pageSize={pageSize}
                  page={page}
                  resizable={false}
                  showPageJump={true}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Page>
    );
  }
}

export default Product;
