import React from 'react';
import PropTypes from 'prop-types';
import utils from '../../utils/utils';
import { Link } from 'react-router-dom';

class RecomendVideo extends React.Component {
  constructor(props){
    super(props);
    let recomend = this.props.recomend || [];
    this.state ={
      recomend
    }
  }
  
  onSelect(id){
    if(this.props.onChange){
      this.props.onChange(id);
    }
  }

  render(){
    let recomend = this.state.recomend;
    return (
      <a href="#" onClick={()=>this.onSelect(recomend._id)}>
        <div className='d-flex py-2 shadow-sm'>
          <img
            alt
            src={utils.getYoutubeThumbnail(recomend.youtube_id)}
            style={{
              width: '100%',
              maxWidth: '10em',
              maxHeight: '7em'
            }}
          />
          <b className='pl-1'>{recomend.title}</b>
        </div>
      </a>
    );
  }
};

RecomendVideo.propTypes = {
  recommend: PropTypes.object.isRequired
};

export default RecomendVideo;
