import React, { Component } from 'react';
import PropTypes from 'prop-types';
import api from '../../utils/api';
import ReactTable from 'react-table';
import Notification from '../../components/Toast';

export default class Staff extends Component {
  static propTypes = {
    company_id: PropTypes.string.isRequired
  };
  state = { staff: [] };
  getData = state => {
    api
      .getPermissionGroupCompany({ company_id: this.props.company_id })
      .then(res => {
        if (res.success)
          this.setState({
            staff: res.data
          });
        else this.notificationSystem.showMessage('failed');
      })
      .catch(err => this.notificationSystem.showMessage('failed'));
  };
  columns = [
    {
      Header: 'Tên nhân viên',
      accessor: 'user_id.name',
      Cell: ({ value }) => value
    },
    {
      Header: 'SDT',
      accessor: '__v',
      Cell: ({ value }) => value
    },
    {
      Header: 'Ngày vào',
      accessor: '__v',
      Cell: ({ value }) => value
    },
    {
      Header: 'SL Đơn hàng',
      accessor: '__v',
      Cell: ({ value }) => value
    },
    {
      Header: 'Tổng thanh toán',
      accessor: '__v',
      Cell: ({ value }) => value
    },
    {
      Header: 'Tổng khuyến mãi',
      accessor: '__v',
      Cell: ({ value }) => value
    },
    {
      Header: 'Tổng hoàn tiền',
      accessor: '__v',
      Cell: ({ value }) => value
    }
    // {
    //   Header: 'Trạng thái',
    //   accessor: 'status',
    //   Cell: ({ value }) => value
    // }
  ];
  render() {
    const { staff } = this.state;
    console.log(staff, this.props.company_id);
    return (
      <>
        <Notification ref={notificationSystem => (this.notificationSystem = notificationSystem)} />
        <ReactTable data={staff} columns={this.columns} onFetchData={this.getData} showPagination={false} pageSize={staff.length} />
      </>
    );
  }
}
