import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Row, Col, Button } from 'reactstrap';
import utils from '../../utils/utils';
import Moment from '../../components/Moment';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';
import Config from '../../utils/config';
export default class Comment extends Component {
  static propTypes = {
    comment: PropTypes.object.isRequired
  };
  onHandleClick(id, status) {
    this.props.onApprove(id, status);
  }
  render() {
    const { comment, onApprove } = this.props;
    return (
      <Row
        className={`border mt-3 rounded-pill shadow-sm ${
          comment.status === 'approved' ? 'border-success' : comment.status === 'pending' ? 'border-warning' : 'border-danger'
        }`}>
        <Col md='1' className='my-auto'>
          <img
            className='rounded-circle'
            height='70'
            width='70'
            src={utils.getAvatarOfUser(comment.user_avatar) || require('../../assets/img/default_user_avatar.png')}
          />
        </Col>
        <Col md='9'>
          <Link to={`${Config.HOST_PREFIX}/user-detail?id=${comment.user_id}`}>
            <b>{comment.user_name}</b>
          </Link>
          <p className='py-3 mb-0' style={{ background: '#eaeaea7a' }}>
            <span className='pl-2'>{comment.content}</span>
          </p>
          <div>
            <small className='text-muted'>
              <Moment fromNow date={comment.updatedAt} />
            </small>
          </div>
        </Col>
        <Col className='m-auto'>
          {comment.status !== 'approved' && (
            <Button color='success' style={{ height: '2.5rem', width: '2.5rem' }} onClick={() => this.onHandleClick(comment._id, 'approved')}>
              <FontAwesomeIcon icon={['fas', 'check']} />
            </Button>
          )}
          {comment.status !== 'rejected' && (
            <Button style={{ height: '2.5rem', width: '2.5rem' }} onClick={() => this.onHandleClick(comment._id, 'rejected')} className='ml-1'>
              <FontAwesomeIcon icon={['fas', 'times']} />
            </Button>
          )}
        </Col>
      </Row>
    );
  }
}
