import Page from '../../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Input } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import Api from '../../utils/api';
import Utils from '../../utils/utils';
import classnames from 'classnames';
import Notification from '../../components/Toast';
import MModal from '../../components/MModal';
import Store from '../companies/Store';
import Staff from '../employees/Staff';
import Moment from '../../components/Moment';

class TableRefund extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      error: null,
      pages: null,
      page: 0,
      pageSize: 10,
      columns: this.defaultColumns,
      status: '',
      showModal: false,
      modal: '',
      company_id: ''
    };
  }

  getData = state => {
    const sort = state.sorted && state.sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString();
    this.setState({
      isLoading: true,
      page: state.page,
      pageSize: state.pageSize
    });
    Api.getHistory({
      page: state.page + 1,
      limit: state.pageSize,
      sort,
      type: 'refund'
      // select: 'images name total_store total_staff total_revenue status'
    }).then(response => {
      if (response.success) {
        this.setState({
          isLoading: false,
          pages: response.total_page,
          data: response.data
        });
      } else {
        this.setState({
          isLoading: false,
          error: 'Tải dữ liệu lỗi.'
        });
      }
    });
  };

  defaultColumns = [
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Ngày
        </span>
      ),
      width: 150,
      accessor: 'createdAt',
      Cell: props => <Moment date={props.value} />,
      maxWidth: 200
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Doanh nghiệp
        </span>
      ),
      width: 250,
      accessor: 'src_company_id.name',
      Cell: ({ value }) => {
        return <b title={value}>{value}</b>;
      }
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Người dùng
        </span>
      ),
      sortable: false,
      width: 250,
      accessor: 'dst_user_id.name',
      Cell: props => {
        return <b>{props.value}</b>;
      }
    },
    // {
    //   Header: () => (
    //     <span>
    //       <i className='fa-tasks' /> Thanh toán
    //     </span>
    //   ),
    //   sortable: false,
    //   width: 200,
    //   accessor: 'value',
    //   Cell: ({ value }) => value
    // }
    // {
    //   Header: 'Khuyến mãi',
    //   accessor: 'discount',
    //   sortable: false,
    //   width: 150,
    //   Cell: ({ value }) => value
    // },
    {
      Header: 'Hoàn tiền',
      maxWidth: 150,
      accessor: 'value',
      Cell: ({ value }) => value
    }
  ];

  onClose = () => {
    this.setState({
      showModal: false
    });
  };
  render() {
    const { data, isLoading, columns, pages, page, pageSize, modal, company_id } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'Company', active: true }]} className='TableRefund'>
        <Notification ref={notificationSystem => (this.notificationSystem = notificationSystem)} />
        <MModal showModal={this.state.showModal} onClose={this.onClose} title={modal.toUpperCase()}>
          {modal === 'store' && <Store company_id={company_id} />}
          {modal === 'staff' && <Staff company_id={company_id} />}
        </MModal>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>Danh sách Hoàn tiền</CardHeader>
              <Row />
              <CardBody>
                <ReactTable
                  loading={isLoading}
                  manual
                  onFetchData={this.getData}
                  data={data}
                  columns={columns}
                  defaultPageSize={10}
                  pages={pages}
                  pageSize={pageSize}
                  page={page}
                  resizable={false}
                  showPageJump={true}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Page>
    );
  }
}

export default TableRefund;
