import Page from '../../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table, Button } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import cellEditFactory from 'react-bootstrap-table2-editor';
import { Link } from 'react-router-dom';

import Api from '../../utils/api';
import Utils from '../../utils/utils';
import queryParser from 'query-string';
import Config from '../../utils/config';
import DateTime from 'react-datetime';
import utils from '../../utils/utils';
import _ from 'lodash';
import api from '../../utils/api';
import Toast from '../../components/Toast';
import Select from 'react-select';

class AddAdmin extends React.Component {
  constructor(props) {
    super(props);

    const query = queryParser.parse(props.location.search);
    if (query.id) {
      this.getData(query.id);
    }

    this.state = {
      isLoading: !!query.id,
      id: query.id,
      name: '',
      nameError: null,
      username: '',
      usernameError: null,
      email: '',
      emailError: null,
      password: '',
      passwordError: null,
      confirmPassword: '',
      confirmPasswordError: null,
      permissionGroupId: '',
      permissionGroupIdError: null,
      status: 'active',
      permissionGroups: []
    };

    api.getAllGroupPermission().then(response => {;
      if (response.success) {;
        this.setState({ permissionGroups: response.data });
      }
    });
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.location.search != this.props.location.search) {
      const query = queryParser.parse(nextProps.location.search);
      if (query.id != this.state.id && query.id) {
        this.getData(query.id);
        this.setState({ isLoading: true, id: query.id });
      }
    }
  }

  getData(id) {
    api.getRewardDetail(id).then(response => {
      if (response.success) {
        const data = response.data;
        this.setState({});
      } else {
        this.showMessage('Lấy thông tin quà tặng thất bại.');
      }
    });
  }

  onChangeField(key, name) {
    const errorKey = key + 'Error';
    this.setState({ [key]: name, [errorKey]: null });
  }

  validateEmail(email) {
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }

  onSubmit() {
    const user = this.state;
    const { id } = user;

    //reset error
    this.setState({
      nameError: null,
      usernameError: null,
      emailError: null,
      passwordError: null,
      confirmPasswordError: null,
      permissionGroupIdError: null
    });

    const error = {};

    if (!user.name || user.name.length <= 0) {
      error.nameError = 'Bạn phải nhập tên. ';
    }

    if (!user.username || user.username.length <= 0) {
      error.usernameError = 'Bạn phải nhập tên đăng nhập.';
    }

    if (!this.validateEmail(user.email)) {
      error.emailError = 'Bạn phải nhập email hợp lệ.';
    }
    if (!user.password || user.password.length < 6) {
      error.passwordError = 'Mật khẩu phải có ít nhất 6 ký tự.';
    }

    if (!user.confirmPassword || user.confirmPassword !== user.password) {
      error.confirmPasswordError = 'Nhập lại mật khẩu không chính xác.';
    }

    // if (!user.permissionGroupId || user.permissionGroupId.length == 0) {
    //   error.permissionGroupIdError = 'Bạn phải chọn quyền.';
    // }

    if (!_.isEmpty(error)) {
      this.setState(error);
      return;
    }
    if (!id) {
      api.createAdmin({ ...this.state,permission_group_id: user.permissionGroupId }).then(response => {
        if (response.success) {
          this.setState({
            name: '',
            email: '',
            username: '',
            permissionGroupId: [],
            password: '',
            confirmPassword: '',
            status: 'actived'
          });
          this.showMessage('Tạo admin thành công');
        } else {
          this.showMessage('Tạo admin thất bại');
        }
      });
    } else {
      api.updateReward(this.state).then(response => {
        if (response.success) {
          this.showMessage('Cập nhật thành công');
        } else {
          this.showMessage('Cập nhật thất bại');
        }
      });
    }
  }

  showMessage(msg) {
    this.toast.showMessage(msg);
  }

  render() {
    const {
      id,
      name,
      nameError,
      username,
      usernameError,
      password,
      passwordError,
      confirmPassword,
      confirmPasswordError,
      permissionGroupId,
      permissionGroupIdError,
      email,
      emailError,
      status,
      permissionGroups
    } = this.state;

    const groups = permissionGroups.map(item=>({value: item._id, label: item.name}));
    return (
      <Page breadcrumbs={[{ name: 'Thêm Admin', active: false }]} className='TablePost'>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>Thêm Admin</CardHeader>
              <CardBody>
                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Họ và tên</div>
                  <div className='col-6 form-group mb-0'>
                    <input
                      value={name}
                      onChange={event => this.onChangeField('name', event.target.value)}
                      placeholder='Nhập họ và tên'
                      className='form-control'
                    />
                    {nameError && <small className='text-danger'>{nameError}</small>}
                  </div>
                </div>

                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Tên đăng nhập</div>
                  <div className='col-6 form-group mb-0'>
                    <input
                      value={username}
                      type='text'
                      placeholder='Nhập tên đăng nhập'
                      onChange={event => this.onChangeField('username', event.target.value)}
                      className='form-control'
                    />
                    {usernameError && <small className='text-danger'>{usernameError}</small>}
                  </div>
                </div>

                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Email</div>
                  <div className='col-6 form-group mb-0'>
                    <input
                      value={email}
                      type='email'
                      placeholder='Nhập email'
                      onChange={event => this.onChangeField('email', event.target.value)}
                      className='form-control'
                    />
                    {emailError && <small className='text-danger'>{emailError}</small>}
                  </div>
                </div>

                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Mật khẩu</div>
                  <div className='col-6 form-group mb-0'>
                    <input
                      value={password}
                      type='password'
                      placeholder='Nhập mật khẩu'
                      onChange={event => this.onChangeField('password', event.target.value)}
                      className='form-control'
                    />
                    {passwordError && <small className='text-danger'>{passwordError}</small>}
                  </div>
                </div>

                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Nhập lại mật khẩu</div>
                  <div className='col-6 form-group mb-0'>
                    <input
                      value={confirmPassword}
                      type='password'
                      placeholder='Nhập lại mật khẩu'
                      onChange={event => this.onChangeField('confirmPassword', event.target.value)}
                      className='form-control'
                    />
                    {confirmPasswordError && <small className='text-danger'>{confirmPasswordError}</small>}
                  </div>
                </div>

                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Trạng thái</div>
                  <div className='col-6 form-group mb-0'>
                    <select value={status} onChange={event => this.onChangeField('status', event.target.value)} className='form-control'>
                      <option value='actived'>Kích hoạt</option>
                      <option value='disabled'>Khóa</option>
                    </select>
                  </div>
                </div>

                <div className='col-12 row mx-0 px-0 mb-3'>
                  <div className='col-4 text-right pt-1 '>Quyền</div>
                  <div className='col-6 form-group mb-0'>
                    <Select
                      value={permissionGroupId}
                      onChange={value => this.onChangeField('permissionGroupId', value)}
                      isMulti={false}
                      options={groups}
                    />
                    {permissionGroupIdError && <small className='text-danger'>{permissionGroupIdError}</small>}
                  </div>
                </div>

                <div className='col-12 row mx-0 justify-content-center'>
                  <button onClick={() => this.onSubmit()} className='btn btn-success mx-2'>
                    {id ? 'Sửa' : 'Thêm'}
                  </button>
                  <Link to={Config.HOST_PREFIX + '/admin'}>
                    <button className='btn btn-danger mx-2'>Hủy</button>
                  </Link>
                </div>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Toast ref={ref => (this.toast = ref)} />
      </Page>
    );
  }
}

export default AddAdmin;
