import React from 'react';
import {Table, Button} from 'reactstrap';
export default class ListMember extends React.Component{
    constructor(props){
        super(props);
    }

    select(index, data){
        if(this.props.onChange){
            this.props.onChange(index,data);
        }
    }

    render(){
        let listMember = this.props.listMember || [];
        let memberRender = []
        for(let i = 0; i < listMember.length; i++){
            memberRender.push(
                <tr>
                    <td>{i+1}</td>
                    <td>{listMember[i].name}</td>
                    <td>{listMember[i].email}</td>
                    <td>
                        <Button
                            color="primary"
                            onClick={()=>this.select(i,listMember[i])}
                        >
                            {"Xóa"}
                        </Button>
                    </td>
                </tr>
            )
        }


        return(
            <Table>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Tên</th>
                        <th>Email</th>
                        <th>Tác vụ</th>
                    </tr>
                </thead>
                <tbody>
                    {memberRender}
                </tbody>
            </Table>
        );
    }
}