import React from 'react';
import {Button} from 'reactstrap';
import Page from '../../components/Page';
import ReactTable from 'react-table';
import apiUser from '../../utils/api.user';
import api from '../../utils/api';
import Toast from '../../components/Toast';

export default class TableListVideoYoutube extends React.Component{
    columns = [
        {
            Header: ()=>(
                <span>Video</span>
            ),
            width:500,
            height: 360,
            accessor: 'youtube_id',
            Cell: props=>(
                <iframe 
                width="100%" 
                height="360" 
                src={`https://www.youtube.com/embed/${props.value}`}
                frameborder="0" 
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
                allowfullscreen>

                </iframe>)
        },
        {
            Header: ()=>(
                <span>Tiêu đề</span>
            ),
            accessor: 'title',
            Cell: props=>(
                <span>{props.value}</span>
            )
        },
        {
            Header: ()=>(
                <span>Thời lượng</span>
            ),
            accessor: 'duration',
            Cell: props=>(
                <span>{props.value ? props.value.substring(2,props.value.length) : ''}</span>
            )
        },
        {
            Header: ()=>(
                <span>Lượt xem</span>
            ),
            accessor: 'viewCount',
            Cell: props => (
                <span>{props.value}</span>
            )
        },
        {
            Header: ()=>(
                <span>Danh mục</span>
            ),
            accessor: 'categories',
            Cell: props => (
                <span>{props.value.category_name}</span>
            )
        },
        {
            Header: ()=>(
                <span>Loại</span>
            ),
            width:100,
            Cell: props =>{
                return(
                    <Button
                        onClick={()=>this.deleteVideo(props.index)}
                    >
                        Loại
                    </Button>   
                )
            }
        }
    ]
    
    
    constructor(props){
        super(props);
        
        let data = this.props.data || [];
        let pages = Math.ceil(data.length/10) || 1;
        let listUser = this.props.listUser || [];
        this.state = {
            pages,
            pageSize: 10,
            page: 0,
            data,
            message:'',
            listUser
        }
    }

    componentWillReceiveProps(nextProps){
        let {pages, data, listUser} = nextProps;
        this.setState({pages, data, listUser});
    }

    deleteVideo(index){
        let {data} = this.state;
        let video_id = data[index].youtube_id;
        data.splice(index, 1);
        data = data.map(data => {
            return data;
        })
        this.setState({data});
        api.cancelVideo(video_id).then(response=>{
            if(response.success){
                this.toast.showMessage('thành công');
            }
            else {
                this.toast.showMessage('đã xảy ra lỗi');
            }
        })
    }

    submitVideos(data){
        this.setState({message:'Đợi chờ là hạnh phúc'});
        let {listUser} = this.state;
        let countUser = listUser.length;
        
        data.forEach(d=>{
            let random = Math.floor(Math.random() * Math.floor(countUser));
            d.user_id = listUser[random]._id;
        });
        
        api.postManyVideo(data).then(response=>{
            if(response.success){
                this.setState({message:'Thành công', data:[]});
                this.toast.showMessage("Thành công");
            }
            else{
                let subMes = response.errors.code == 11000 ? ': lỗi trùng video' : '';
                this.setState({message: 'thất bại là mẹ thành công' + subMes, data:[]});
                this.toast.showMessage("Thất bại là mẹ thành công");
            }
        })
    }

    getData = info => {
        let {page, pages, pageSize} = info;
        let data = this.state.data;
        pages = Math.ceil(data.length/pageSize);
        this.setState({page, pages, pageSize});
    }

    render(){
        let {page, pages, pageSize, data, message} = this.state;
        return(
            
            <Page style={{width: "100%"}}>
                <ReactTable
                    className='overflow-hidden'
                    columns={this.columns}
                    data={data}
                    page={page}
                    pages={pages}
                    pageSize={pageSize}
                    onFetchData={this.getData}
                />

                <Button
                    onClick={()=>this.submitVideos(data)}
                >
                    Post video
                </Button>
                <div className="text-center">{message}</div>
                <Toast ref={(ref) => this.toast = ref} />
            </Page>
        )
    }

}