import React from 'react';
import Popup from 'reactjs-popup'

export class Modal extends React.Component {
    state = {
        name: '',
        description: ''
    }

    changeValue(key,value){
        this.setState({[key]: value});
    }

    addGroup(){
        if(this.props.onAdd){
            this.props.onAdd(this.state);
        }
    }

    cancel(){
        this.setState({name: '', description: ''});
        if(this.props.onClose){
            this.props.onClose();
        }
    }

    render() {
        const props = this.props;
        const {name,description} = this.state;
        return (<Popup
            modal
            open={props.open}
            onClose={props.onClose}
            position="top center"
            closeOnDocumentClick>
            <div style={{ minWidth: '300px', minHeight: '100px' }} class="px-2 py-2">
                <h4>Thêm nhóm</h4>
                <div class="form">
                    <div>
                        <label>Tên nhóm</label>
                        <input value={name} onChange={(e)=>this.changeValue('name',e.target.value)} class="form-control" placeholder="Nhập tên nhóm" />
                    </div>
                    <div>
                        <label>Mô tả</label>
                        <textarea  value={description} onChange={(e)=>this.changeValue('description',e.target.value)}
                         rows={3} class="form-control" placeholder="Nhập mô tả (không bắt buộc)" />
                    </div>
                    <div class="row justify-content-center my-2">
                        <button class="btn btn-success mx-2" onClick={()=>this.addGroup()}>Thêm</button>
                        <button class="btn btn-secondary mx-2" onClick={()=>this.cancel()}>Hủy</button>
                    </div>
                </div>
            </div>
        </Popup>
        );
    }
}

export default Modal;