import React from 'react';
import { Row, Col, Button, DropdownButton, Dropdown, Form } from 'react-bootstrap';
import api from '../../utils/api';
import Toast from '../../components/Toast';

const youtubeKey = 'AIzaSyDP_eDEJBlT7NeF-vLiL1j6k731knrswx0';
const regionCodes = ['VN', 'US', 'AU', 'KR', 'CN', 'JP'];
const videoDurations = ['short','medium'];
const optionMaxResult = [20,30,40,50];
export default class UploadYoutubeTool extends React.Component{       
    constructor(props){
        super(props);
        this.state = {
            urlTypes: [
                {url:`https%3A%2F%2Fwww.googleapis.com%2Fyoutube%2Fv3%2Fvideos%3Fpart%3Dsnippet%2CcontentDetails%2Cstatistics%26chart%3DmostPopular%26hl%3Dvi%26key%3D${youtubeKey}`,name:'Top thịnh hành', type:'mostPopular'},
                {url:`https%3A%2F%2Fwww.googleapis.com%2Fyoutube%2Fv3%2Fsearch%3Fpart%3Dsnippet%26key%3D${youtubeKey}%26type%3Dvideo%26relevanceLanguage%3Dvi`,name: 'Tìm kiếm', type:'search'}],
            index: {
                url: 0,
                category: 0,
                region: 0,
                duration:0,
                maxResults: 0
            },
            // maxResults: 50,
            regionCodes,
            videoDurations,
            categories: [],
            searchKey:'',
            message:'',
            optionMaxResult,
            // videoDuration: 'short',
            data: []
        }
    }

    componentDidMount(){
        api.getCategory("active").then(response=>{
            if(response.success){
                this.setState({categories: response.data});
            }
            else{
                this.toast.showMessage('đã xảy ra lỗi lấy danh sách danh mục');
            }
        })
    }

    select(selectIndex, key){
        let {index} = this.state;
        if(index[key] !== selectIndex) {
            index[key] = selectIndex;
            this.setState({index});
        }
    }

    deleteVideo(index){
        let {data} = this.state;
        data.splice(index,1);
        let dataUpdate = data;
        this.setState({data: dataUpdate});
    }

    inputSearchKey(input){
        this.setState({searchKey: input})
    }

    onGet(){
        this.setState({message:'đợi xíu'});
        let {urlTypes,index,regionCodes, categories, searchKey, videoDurations, optionMaxResult} = this.state;

        api.getVideosYoutube({
            url: urlTypes[index.url].url,
            url_type: urlTypes[index.url].type,
            regionCode: regionCodes[index.region],
            maxResults: optionMaxResult[index.maxResults],
            videoCategoryId: categories[index.category].code,
            search_key: searchKey,
            videoDuration: videoDurations[index.duration]
        }).then(response=>{
            if(response.success){
                if(this.props.onChange) this.props.onChange(response.data);
                this.setState({data: response.data, message:'xong roi'});
            }
            else{
                this.setState({message: 'Lỗi'})
            }
        })
    }



    render(){
        
        let {urlTypes, index,message, regionCodes, categories, data} = this.state;
        let disableInput = index.url === 0? true : false; // mostpopular not neet search key

        let tablePages = Math.ceil(data.length/10);
        
        let dropItems = [], categoryItems = [], regionItems=[], videoDurationItems=[], maxResultItems=[];
        urlTypes.forEach((url, index)=>{
            dropItems.push(
                <Dropdown.Item  onClick={()=>this.select(index, 'url')}>
                    {url.name}
                </Dropdown.Item>
            )
        });
        categories.forEach((category, index)=>{
            categoryItems.push(
                <Dropdown.Item onClick={()=>this.select(index,'category')}>
                    {category.name}
                </Dropdown.Item>
            )
        });
        regionCodes.forEach((region, index)=>{
            regionItems.push(
                <Dropdown.Item onClick={()=>this.select(index,'region')}>
                    {region}
                </Dropdown.Item>
            )
        });
        videoDurations.forEach((duration, index)=>{
            videoDurationItems.push(
                <Dropdown.Item onClick={()=>this.select(index, 'duration')}>
                    {duration}
                </Dropdown.Item>
            )
        });
        optionMaxResult.forEach((result,index)=>{
            maxResultItems.push(
                <Dropdown.Item onClick={()=>this.select(index, 'maxResults')}>
                    {result}
                </Dropdown.Item>
            )
        })


        return(
            

            <Form className="ml-3 mt-3">
                <Form.Group as={Row} controlId="formPlaintextEmail">
                    <Form.Label column sm="2">
                    ....:
                    </Form.Label>
                    <Col sm="10">
                    <DropdownButton size={"block"} title={urlTypes[index.url].name}>
                        {dropItems}
                    </DropdownButton>
                    </Col>
                </Form.Group>

                <Form.Group as={Row} controlId="formPlaintextPassword">
                    <Form.Label column sm="2">
                    Chọn thể loại
                    </Form.Label>
                    <Col sm="10">
                        <DropdownButton size={"block"} disabled={disableInput} title={categories[index.category]? categories[index.category].name : ""}>
                            {categoryItems}
                        </DropdownButton>                    
                    </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="formPlaintextPassword">
                    <Form.Label column sm="2">
                    Chọn số kết quả trả về
                    </Form.Label>
                    <Col sm="10">
                        <DropdownButton size={"block"} title={optionMaxResult[index.maxResults]}>
                            {maxResultItems}
                        </DropdownButton>                    
                    </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="formPlaintextPassword">
                    <Form.Label column sm="2">
                        Chọn nước:
                    </Form.Label>
                    <Col sm="10">
                        <DropdownButton size={"block"} title={regionCodes[index.region]}>
                            {regionItems}
                        </DropdownButton>                        
                    </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="formPlaintextPassword">
                    <Form.Label column sm="2">
                        Thời lượng:
                    </Form.Label>
                    <Col sm="10">
                        <DropdownButton disabled={disableInput} size={"block"} title={videoDurations[index.duration]}>
                            {videoDurationItems}
                        </DropdownButton>                        
                    </Col>
                </Form.Group>
                <Form.Group as={Row}>
                    <Form.Label column sm="2">
                    Từ khóa:
                    </Form.Label>
                    <Col sm="10">
                        <Form.Control disabled={disableInput} onChange={(event)=>this.inputSearchKey(event.target.value)}></Form.Control>
                    </Col>
                    
                </Form.Group>
                <Button onClick={()=>this.onGet()}>Submit</Button>

                <div className="text-center">
                    <span>{message}</span>
                </div>

                <Toast ref={(ref)=> this.toast = ref} />
            </Form>
        )
    }
}