var subArray = [];
let mainState = {};

let stringMainState = localStorage.getItem('appState');
if(stringMainState) {
    try{
        mainState = JSON.parse(stringMainState);
    }
    catch(err){
        mainState = {}
    }
}
    
    

function subcribe(callback) {
    if (subArray.indexOf(callback) < 0)
        return subArray.push(callback);
}

function unSubcribe(callback) {
    let position = subArray.indexOf(callback);
    if (position >= 0) {
        return subArray.splice(position, 1);
    }
}

async function notice(state) {
    mainState = { ...mainState, ...state };
    localStorage.setItem('appState',JSON.stringify(mainState));
    for (let i = 0; i < subArray.length; i++) {
        try {
            await subArray[i](mainState);
        }
        catch (err) {
            console.log('----------NOTICE CALL FUNCTION------', err);
        }
    }
}

function getState(){
    return mainState;
}

export default {
    subcribe,
    unSubcribe,
    notice,
    getState,
}