import React from 'react';
import ReactTable from 'react-table';
import Toast from '../../components/Toast';
import { Card, CardBody, CardHeader, Col, Row, Button } from 'reactstrap';
import { Nav } from 'react-bootstrap';
import Page from '../../components/Page';
import api from '../../utils/api';

export default class Promotion extends React.Component {

  columns = [
    {
      Header: () => (
        <span className='fa-task'>Ngày lập</span>
      ),
      accessor: 'createdAt',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Tên</span>
      ),
      accessor: 'name',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Doanh Nghiệp</span>
      ),
      accessor: 'company_id',
      Cell: props => <div>{props.value? props.value.name : ''}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Bắt đầu</span>
      ),
      accessor: 'start_at',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Kết thúc</span>
      ),
      accessor: 'expire_at',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Khuyến mãi</span>
      ),
      accessor: 'value',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Hoàn tiền</span>
      ),
      accessor: 'refund',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Đã lấy</span>
      ),
      accessor: 'remain',
      Cell: props => <div>{props.original.total - props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Còn lại</span>
      ),
      accessor: 'remain',
      Cell: props => <div>{props.value}</div>
    },
  ]

  constructor(props) {
    super(props);
    this.state = {
      listPromotion: [],
      pageSize: 20,
      sort: '-createdAt',
      page: 0,
      isLoading: true,
      pages: null,
      date_status: 'running'
    }
  }

  getData(info) {
    let { page, pageSize, sorted } = info;
    let sort = sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString();

    api.getPromotion({ date_status: this.state.date_status, limit: pageSize, page: page + 1, sort }).then(response => {
      if (response.success) {
        this.setState({ listPromotion: response.data, pageSize: info.pageSize, page: page, isLoading: false, pages: response.totalPage, sort });
      }
      else {
        this.toast.showMessage('get promotion failed');
      }
    })
  }

  selectCategory(e) {
    let { limit, page, sort } = this.state;
    api.getPromotion({ limit, page, date_status: e, sort }).then(response => {
      if (response.success) {
        let { data, totalPage } = response;
        this.setState({ listPromotion: data, pages: totalPage, date_status: e });
      }
      else {
        this.toast.showMessage('get promotion failed');
      }
    })
  }

  render() {
    let { isLoading, pageSize, page, pages, listPromotion } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'Khuyen mai', active: true }]} className='TablePost'>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>
                Danh sách Khuyến mãi
              </CardHeader>
              <Nav
                variant='tabs'
                defaultActiveKey={'running'}
                onSelect={e => {
                  this.selectCategory(e);
                }}
              >
                <Nav.Item>
                  <Nav.Link eventKey={'waiting'}>Đang chờ</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey={'running'}>Hoạt động</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey={'overed'}>Hết hạn</Nav.Link>
                </Nav.Item>
              </Nav>
              <CardBody>
                <ReactTable
                  className='overflow-hidden'
                  data={listPromotion}
                  columns={this.columns}
                  defaultPageSize={20}
                  page={page}
                  pages={pages}
                  pageSize={pageSize}
                  onFetchData={(info) => this.getData(info)}
                  showPageJump={true}
                  manual
                  loading={isLoading}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Toast ref={(ref) => this.toast = ref} />
      </Page>
    )
  }
}