import React, { Component } from 'react';
import api from '../../utils/api';
import { Container, Row, Col, Input } from 'reactstrap';
import { isEmpty } from 'lodash';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Comment from '../comments/Comment';
import RecomendVideo from './RecomendVideo';
import Select from 'react-select';
import mergeObject from '../../utils/merge-object';

export default class Video extends Component {
  constructor(props) {
    super(props);
    let video_id = this.props.video_id || null;
    this.state = {
      video: {},
      recommends: [],
      comments: [],
      categories: [],
      video_id
    };
  }
  componentDidMount() {
    // this.getData(this.props.match.params.id);
    this.getData(this.state.video_id);
    api.getCategory('active').then(res => {
      if (res.success) {
        const categories = res.data.map(el => ({
          value: el._id,
          label: el.name
        }));
        this.setState({
          categories
        });
      }
    });
  }
  componentWillReceiveProps(NextProps) {
    // if (NextProps.match.params.id !== this.props.match.params.id) this.getData(NextProps.match.params.id);
    if(NextProps.video_id !== this.props.video_id){
      this.getData(NextProps.id);
      this.setState({video_id: NextProps.video_id});
    }
  }
  getData(id, sw) {
    api.getVideoById(id).then(res => {
      if (res.success) {
        res.data.categories = res.data.categories.map(per => ({ value: per.category_id, label: per.category_name }));
        this.setState({ video: res.data });
      }
    });
    api.getComments({ video_id: id }).then(res => {
      if (res.success) {
        this.setState({ comments: res.data });
      }
    });
    api.getVideoRecommend(id).then(res => {
      if (res.success) {
        this.setState({ recommends: res.data });
      }
    });
  }
  onApproveComment = (id, status) => {
    api.updateComment({ id, status }).then(response => {
      if (response.success) {
        if (status === 'rejected')
          this.setState({
            comments: this.state.comments.filter(item => item._id !== id)
          });
      }
    });
  };

  changeStatus = status => {
    this.setState(prevState => {
      this.onSave(prevState.video._id, status);
      return { video: { ...prevState.video, status } };
    });
  };
  changeCategory = values => {
    this.setState(
      prevState => ({ video: { ...prevState.video, categories: values } }),
      this.onSave(this.state.video._id, this.state.video.status, values)
    );
  };
  onSave = (id, status, categories) => {
    const data = mergeObject({}, { video_id: id, status, categories: categories && categories.map(cate => cate.value).toString() });
    api.changeVideoStatus(data).then(res => {
      if (res.success) {
        res.data.categories = res.data.categories.map(per => ({ value: per.category_id, label: per.category_name }));
        this.setState({ video: res.data });
      }
    });
  };
  selectVideo(id){
    if(this.state.video_id !== id){
      this.getData(id);
      this.setState({video_id: id});
    }
}

  render() {
    const { video, comments, recommends } = this.state;
    const commentList = comments && comments.map((cmt, key) => <Comment comment={cmt} key={key} onApprove={this.onApproveComment} />);
    const recommendList = recommends && recommends.map((rcm, key) => <RecomendVideo onChange={(id)=>this.selectVideo(id)} recomend={rcm} key={key} />);
    if (!isEmpty(video))
      return (
        <Container>
          <Row>
            <Col md='8'>
              <iframe height='400px' width='700px' src={'https://www.youtube.com/embed/' + video.youtube_id} />
              <b>{video.title}</b>
              <div style={{ display: 'flex', color: 'crimson' }}>
                <span className='text-muted mr-auto my-auto'>{video.views_count.toLocaleString()} lượt xem</span>
                <span style={{ display: 'block', padding: 10 }}>
                  <FontAwesomeIcon className='mr-2' icon={['fas', 'thumbs-up']} />
                  <small>{video.likes_count.toLocaleString()}</small>
                </span>
                <span style={{ display: 'block', padding: 10 }}>
                  <FontAwesomeIcon className='mr-2' icon={['fas', 'thumbs-down']} />
                  <small>{video.dislikes_count.toLocaleString()}</small>
                </span>
                <span style={{ display: 'block', padding: 10 }}>
                  <FontAwesomeIcon className='mr-2' icon={['fa', 'share-alt']} />
                  <small>{video.shares_count.toLocaleString()}</small>
                </span>
              </div>
              <div style={{ width: '70%', display: 'inline-block' }}>
                <Select value={video.categories} isMulti onChange={this.changeCategory} options={this.state.categories} />
              </div>
              <div style={{ display: 'inline-block', verticalAlign: 'middle', margin: 10 }}>
                <Input type='select' name='status' id='exampleSelect' value={video.status} onChange={e => this.changeStatus(e.target.value)}>
                  <option disabled>status</option>
                  <option value='approved'>Kích hoạt</option>
                  <option value='rejected'>Vô hiệu</option>
                </Input>
              </div>
              <hr
                style={{ height: 3 }}
                className={video.status === 'approved' ? 'bg-success' : video.status === 'rejected' ? 'bg-danger' : 'bg-warning'}
              />
            </Col>

            <Col>{!isEmpty(recommends) && recommendList}</Col>
          </Row>
          <div>
            <b>{video.comments_count.toLocaleString()} bình luận</b>
            {!isEmpty(comments) && commentList}
          </div>
        </Container>
      );
    else return <></>;
  }
}
