import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Row, Col, Container } from 'react-bootstrap';
import api from '../utils/api';
import utils from '../utils/utils';

export default class UserDetail extends Component {
  state = {
    user: {}
  };
  componentDidMount() {
    api.getUserInfo(this.props.user_id).then(res => {
      if (res.success === true) {
        this.setState({ user: res.data });
      }
    });
  }
  static propTypes = {
    user_id: PropTypes.string.isRequired
  };
  render() {
    const { user } = this.state;
    return (
      <Container>
        <Row>
          <Col md='4' className='border shadow-sm'>
            <div className="text-center">
              <img width='200' height='200' src={utils.getAvatarOfUser(user.avatar, user.fb_id)} style={{ objectFit: 'cover' }} />
            </div>
          </Col>
          <Col md='8' className='border shadow-sm'>
            <Row>
              <Col md='3'>Họ tên:</Col>
              <Col>{user.name}</Col>
            </Row>
            <Row>
              <Col md='3'>Ngày đăng ký:</Col>
              <Col>{user.createdAt}</Col>
            </Row>
            <Row>
              <Col md='3'>Số điện thoại:</Col>
              <Col>{user.phone}</Col>
            </Row>
            <Row>
              <Col md='3'>Email:</Col>
              <Col>{user.email}</Col>
            </Row>
          </Col>
        </Row>
        <hr />
        <Row>
          <Col md='6' className='border shadow-sm'>
            <Row>
              <Col md='4'>Tổng Video đăng:</Col>
              <Col>{user.email}</Col>
            </Row>
            <Row>
              <Col md='4'>Tổng lượt xem video:</Col>
              <Col>{user.view}</Col>
            </Row>
            <Row>
              <Col md='4'>Tổng điểm Video:</Col> <Col>{user.point}</Col>
            </Row>
            <Row>
              <Col md='4'>Đổi quà: </Col>
              <Col>{user.email}</Col>
            </Row>
          </Col>
          <Col md='6' className='border shadow-sm'>
            <Row>
              <Col md='4'>Hạng thành viên: </Col>
              <Col>{user.level}</Col>
            </Row>
            <Row>
              <Col md='4'>Số dư tài khoản: </Col>
              <Col>{user./* wallet. */ total}</Col>
            </Row>
            <Row>
              <Col md='4'>Tổng tiền nạp: </Col>
              <Col>{user./* wallet. */ deposit}</Col>
            </Row>
            <Row>
              <Col md='4'>Tổng tiền rút: </Col>
              <Col>{user./* wallet} */ rut}</Col>
            </Row>
            <Row>
              <Col md='4'>Tổng tiền chuyển: </Col>
              <Col>{user./* wallet} */ tranfer}</Col>
            </Row>
            <Row>
              <Col md='4'>Tổng tiền thanh toán: </Col>
              <Col>{user.total_pay}</Col>
            </Row>
            <Row>
              <Col md='4'>Tổng tiền Tiêu dùng: </Col>
              <Col>{user.total_buy}</Col>
            </Row>
            <Row>
              <Col md='4'>Tổng Hoàn Tiền: </Col>
              <Col>{user./* wallet. */ refund}</Col>
            </Row>
            <Row>
              <Col md='4'>Tổng Hoa hồng: </Col>
              <Col>{user./* wallet. */ commision}</Col>
            </Row>
            <Row>
              <Col md='4'>Tổng Mã Khuyến Mãi: </Col>
              <Col>{user.total_promotion_code}</Col>
            </Row>
          </Col>
        </Row>
      </Container>
    );
  }
}
