import Page from '../../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Input, Button } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import Api from '../../utils/api';
import { Nav } from 'react-bootstrap';
import MModal from '../../components/MModal';
import SApproveCategory from './SApproveCategory';
import Notification from '../../components/Toast';
import classnames from 'classnames';
import Config from '../../utils/config';

class CompanyCategory extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      error: null,
      pages: null,
      page: 0,
      pageSize: 20,
      status: '',
      tab: 1,
      showModal: false,
      category: {}
    };
    this.getData = this.getData.bind(this);
  }

  getData(state) {
    const sort = state.sorted && state.sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString();
    this.setState({ isLoading: true });
    Api.getSCategories({
      status: state.status !== undefined ? state.status : this.state.status,
      page: state.page + 1,
      limit: state.pageSize,
      type: state.tab || this.state.tab,
      sort
    })
      .then(response => {
        if (response.success) {
          this.setState({
            isLoading: false,
            data: response.data,
            pages: response.total_page,
            page: state.page,
            pageSize: state.pageSize
          });
        } else {
          this.notificationSystem.showMessage('failed');
          this.setState({
            isLoading: false,
            error: 'Tải dữ liệu lỗi.'
          });
        }
      })
      .catch(err => this.notificationSystem.showMessage('failed'));
  }

  onClose = () => {
    this.setState({
      showModal: false
    });
  };

  onToggleActive = (id, status) => {
    Api.approveSCategory({ id, status })
      .then(res => {
        if (res.success) {
          this.onUpdateData(id, res.data);
        } else this.notificationSystem.showMessage('failed');
      })
      .catch(err => this.notificationSystem.showMessage('failed'));
  };

  onUpdateData = (id, updateData) => {
    const index = this.state.data.findIndex(el => el._id === id);
    const newData = [...this.state.data];
    if (index >= 0) {
      newData[index] = updateData;
      this.setState({ data: newData });
    } else this.getData(this.state);
  };

  defaultColumns = [
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tên danh mục cha
        </span>
      ),
      accessor: 'parent_id',
      Cell: ({ value }) => (value ? value.name : ''),
      maxWidth: 200
    },
    {
      Header: () => (
        <span>
          <i className='fa-tasks' /> Tên Danh mục
        </span>
      ),
      accessor: 'name',
      style: { overflow: 'visible', whiteSpace: 'normal', fontWeight: 'bold' },
      Cell: ({ value }) => {
        return value;
      }
    },
    {
      Header: 'Người tạo',
      maxWidth: 200,
      accessor: d => [d.user_id, d.admin_id],
      id: 'creator',
      Cell: ({ value }) => {
        return value[0] ? `${value[0].name}/user` : `${value[1].name}/admin`;
      }
    },
    {
      Header: 'Trạng thái',
      maxWidth: 125,
      accessor: 'status',
      Cell: props => (
        <>
          <Input
            className={classnames({
              'bg-success': props.value === 'active',
              'bg-dark': props.value === 'disabled',
              'bg-warning': props.value === 'pending',
              'text-white': props.value !== 'pending'
            })}
            type='select'
            name='status'
            id='exampleSelect'
            value={props.value}
            onChange={e => this.onToggleActive(props.original._id, e.target.value)}
          >
            <option value='active'>Kích hoạt</option>
            <option disabled hidden value='pending'>
              Đang chờ
            </option>
            <option value='disabled'>Vô hiệu</option>
          </Input>
        </>
      ),
      filterable: true,
      Filter: ({ filter, onChange }) => {
        return (
          <select
            onChange={event => {
              this.setState({ status: event.target.value });
              onChange();
            }}
            style={{ width: '100%' }}
            value={this.state.status}
          >
            <option value=''>Tất cả</option>
            <option value='active'>Đã duyệt</option>
            <option value='pending'>Đang chờ</option>
            <option value='disabled'>Vô hiệu</option>
          </select>
        );
      }
    }
  ];

  render() {
    const { data, isLoading, pages, page, pageSize, tab, category, status } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'Company', active: true }]} className='CompanyCategory'>
        <Notification ref={notificationSystem => (this.notificationSystem = notificationSystem)} />
        <MModal
          showModal={this.state.showModal}
          onClose={this.onClose}
          title={'Danh mục ' + (+tab === 1 ? 'cha' : +tab === 2 ? 'con' : 'dòng sản phẩm')}
        >
          <SApproveCategory category={category} type={tab} onUpdateData={this.onUpdateData} onClose={this.onClose} />
        </MModal>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>Danh sách danh mục</CardHeader>
              <Nav
                variant='tabs'
                defaultActiveKey={1}
                onSelect={e => {
                  this.setState({ tab: +e, status: '' });
                  this.getData({ ...this.state, tab: +e, page: 0, pages: null, status: '' });
                }}
              >
                <Nav.Item>
                  <Nav.Link eventKey={1}>Danh mục cha</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey={2}>Danh mục con</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey={3}>Dòng sản phẩm</Nav.Link>
                </Nav.Item>
              </Nav>
              <Button className='w-auto ml-auto mr-3' onClick={() => this.setState({ showModal: true, category: {} })}>
                Thêm
              </Button>
              <CardBody>
                {tab === 1 && (
                  <ReactTable
                    loading={isLoading}
                    manual
                    onFetchData={this.getData}
                    data={data}
                    columns={+tab === 1 ? this.defaultColumns.slice(1) : this.defaultColumns.slice(0)}
                    defaultPageSize={20}
                    pages={pages}
                    pageSize={pageSize}
                    page={page}
                    resizable={false}
                    showPageJump={true}
                  />
                )}
                <ReactTable
                  loading={isLoading}
                  manual
                  onFetchData={this.getData}
                  data={data}
                  columns={+tab === 1 ? this.defaultColumns.slice(1) : this.defaultColumns.slice(0)}
                  defaultPageSize={20}
                  pages={pages}
                  pageSize={pageSize}
                  page={page}
                  resizable={false}
                  showPageJump={true}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Page>
    );
  }
}

export default CompanyCategory;
