import React from 'react';
import ReactTable from 'react-table';
import { Card, CardBody, CardHeader, Col, Row, Input } from 'reactstrap';
import { Nav } from 'react-bootstrap';
import Page from '../../components/Page';
import Toast from '../../components/Toast';
import api from '../../utils/api';

export default class Transactions extends React.Component {
  columns = [
    {
      Header: () => (
        <span className='fa-task'>Ngày</span>
      ),
      accessor: 'createdAt',
      Cell: props => <div>{props.value.substring(0, 19)}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Tên</span>
      ),
      accessor: 'user_id',
      Cell: props => <div>{props.value ? props.value.name : ''}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Số tiền</span>
      ),
      accessor: 'value',
      Cell: props => <div>{props.value ? props.value : ''}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Ngân hàng</span>
      ),
      accessor: 'bank',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Số dư cũ</span>
      ),
      accessor: 'old_balance',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Số dư mới</span>
      ),
      accessor: 'new_balance',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Trạng thái</span>
      ),
      accessor: 'status',
      Cell: props => <div>{props.value}</div>
    }
  ]

  constructor(props) {
    super(props);
    this.state = {
      page: 0,
      pages: null,
      pageSize: 10,
      sort: '-createdAt',
      data: [],
      type: 'deposit',
      users: [],
      user: { name: 'Người dùng', _id: '' }
    }
  }

  componentDidMount() {
    api.getUsers().then(response => {
      if (response.success) {
        this.setState({ users: response.data });
      }
      else {
        this.toast.showMessage('Lỗi tải dữ liệu người dùng');
      }
    })
  }

  selectCategory(e) {
    let { page, pageSize, sort, user } = this.state;
    api.getSearchTransactions({ limit: pageSize, page: page + 1, sort, type: e, user_id: user._id }).then(response => {
      if (response.success) {
        this.setState({ type: e, pages: response.totalPage, data: response.data })
      }
      else {
        this.toast.showMessage('đã xảy ra lỗi');
      }
    })
  }

  getData = (info) => {
    let { page, pageSize, sorted } = info;
    let { type, user } = this.state;
    let sort = sorted ? sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString() : this.state.sort;
    api.getSearchTransactions({ limit: pageSize, page: page + 1, sort, type, user_id: user._id }).then(response => {
      if (response.success) {
        this.setState({ data: response.data, page, pageSize, sort, pages: response.totalPage });
      }
      else {
        this.toast.showMessage('đã xảy ra lỗi');
      }
    })
  }

  onChange = event => {
    let index = event.target.selectedIndex - 1;
    let { users } = this.state;
    this.setState({ user: users[index] });
    let state = this.state;
    state.user = users[index];
    this.getData({ ...state });
  }

  render() {
    let { page, pages, pageSize, sort, data, users, user } = this.state;
    let userOptionItems = [];
    users.forEach((user, index) => {
      userOptionItems.push(
        <option key={index} value={user.name}>{user.name}</option>
      )
    })


    return (
      <Page>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>
                Danh sách Giao dịch
              </CardHeader>
              <Nav
                variant='tabs'
                defaultActiveKey={'deposit'}
                onSelect={e => {
                  this.selectCategory(e);
                }}
              >
                <Nav.Item>
                  <Nav.Link eventKey={'deposit'}>Nạp tiền</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey={'withdrawal'}>Rút tiền</Nav.Link>
                </Nav.Item>
              </Nav>
              <Input
                className=' ml-5 d-inline'
                type='select'
                name='user'
                value={user ? user.name : ''}
                // id = {user? user._id}
                style={{ width: 200 }}
                onChange={this.onChange}
              >
                <option value=''>Người dùng</option>
                {userOptionItems}
              </Input>

              <CardBody>
                <ReactTable
                  className='overflow-hidden'
                  data={data}
                  columns={this.columns}
                  // defaultPageSize={20}
                  defaultPageSize={pageSize}
                  page={page}
                  pages={pages}
                  pageSize={pageSize}

                  onFetchData={this.getData}
                  showPageJump={true}
                  manual
                // loading={isLoading}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Toast ref={(ref) => this.toast = ref} />
      </Page>
    )
  }


}