import GAListener from './components/GAListener';
import All from './All';
import Login from './Login';
import Signup from './Signup';
import React from 'react';
import componentQueries from 'react-component-queries';
import { BrowserRouter, Redirect, Switch } from 'react-router-dom';
import './../node_modules/sweetalert/dist/sweetalert.css';
import './styles/reduction.scss';
import stateManager from './stateManager/stateManager';
import Api from './utils/api';
import Config from './utils/config';
import queryParser from 'query-string';
import { library } from '@fortawesome/fontawesome-svg-core'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { faCheckSquare, faCoffee } from '@fortawesome/free-solid-svg-icons'
library.add(fab, faCheckSquare, faCoffee)

const getBasename = () => {
  return '/';
};

class App extends React.Component {
  constructor(props) {
    super(props);
    let gotoDashboard = false;
    let state = stateManager.getState();
    if(state.isLogin && state.admin){
      gotoDashboard = true;
      Api.setToken(state.admin.access_token);
    }
    this.state = {
      gotoDashboard,
      redirectTo: null
    };
  }

  componentDidMount() {
    this.updateAuth = state => {
      this.setState({ gotoDashboard: state.isLogin });
    };
    stateManager.subcribe(this.updateAuth);
  }
  componentWillUnmount() {
    stateManager.unSubcribe(this.updateAuth);
  }

  render() {
    const query = queryParser.parse(window.location.search);
    let mustRedirect = false;
    if(query && query.originUrl){
      mustRedirect = true;
    } 
    
    if (!this.state.gotoDashboard) {
      return (
        <BrowserRouter basename={getBasename()}>
          <GAListener>
            <Switch>
              {Login()}
              {Signup()}

            { mustRedirect?<Redirect to={query.originUrl }/>:
              <Redirect to={Config.HOST_PREFIX + '/login'} />}
            </Switch>
          </GAListener>
        </BrowserRouter>
      );
    }
    return (
      <BrowserRouter basename={getBasename()}>
        <GAListener>
          <Switch>
            {All()}
            { mustRedirect?<Redirect to={query.originUrl}/>:
            <Redirect to={Config.HOST_PREFIX} />}
          </Switch>
        </GAListener>
      </BrowserRouter>
    );
  }
}

const query = ({ width }) => {
  if (width < 575) {
    return { breakpoint: 'xs' };
  }

  if (576 < width && width < 767) {
    return { breakpoint: 'sm' };
  }

  if (768 < width && width < 991) {
    return { breakpoint: 'md' };
  }

  if (992 < width && width < 1199) {
    return { breakpoint: 'lg' };
  }

  if (width > 1200) {
    return { breakpoint: 'xl' };
  }

  return { breakpoint: 'xs' };
};

export default componentQueries(query)(App);
