import React from 'react';
import {
  MdClearAll,
} from 'react-icons/md';
import {
  Button,
  Nav,
  Navbar,
} from 'reactstrap';
import bn from './../../utils/bemnames';
import Api from './../../utils/api';
import { Redirect } from 'react-router-dom';
import stateManager from '../../stateManager/stateManager';
import apiUser from '../../utils/api.user';
const bem = bn.create('header');

class Header extends React.Component {

  state = {
    gotoLogin: false,
    username: stateManager.getState().admin.email || 'Admin',
  };

  constructor(props) {
    super(props);
    this.handleLogin = this.handleLogin.bind(this);
  }

  componentDidMount() {
    stateManager.subcribe(this.handleLogin);
  }

  componentWillUnmount() {
    stateManager.unSubcribe(this.handleLogin);
  }

  handleLogin(data) {
    console.log(data);
    if(data.admin)
      this.setState({ username: data.admin.email });
  }

  logOut() {
    Api.setToken(null);
    apiUser.setToken(null);
    stateManager.notice({ isLogin: false, users:[], admin:null, permissionGroup:null, firstHandel: null, submenu:'', indexActiveSibar:null});
    this.setState({ gotoLogin: true });
  }

  handleSidebarControlButton = event => {
    event.preventDefault();
    event.stopPropagation();

    document.querySelector('.cr-sidebar').classList.toggle('cr-sidebar--open');
  };

  render() {
    // if (this.state.gotoLogin) return <Redirect to={Config.HOST_PREFIX + '/login'} />
    return (
      <Navbar light expand className={bem.b('bg-white')}>
        <Nav navbar className="mr-2">
          <Button outline onClick={this.handleSidebarControlButton}>
            <MdClearAll size={25} />
          </Button>
        </Nav>

        <Nav navbar className={bem.e('nav-right')}>
          Hello <b className="ml-2">{this.state.username}</b>,
           <div
            className="ml-2"
            onClick={() => this.logOut()}
            style={{ cursor: 'pointer', textDecoration: 'underline' }}>Logout</div>
        </Nav>
      </Navbar>
    );
  }
}

export default Header;
