import React from 'react';
import ReactTable from 'react-table';
import Toast from '../../components/Toast';
import {Row, Col, Card, CardBody, CardHeader} from  'reactstrap';
import Page from '../../components/Page';
import api from '../../utils/api';

const type = {typist: 'Nhập liệu', cashier:'Thu ngân', seller:'Thanh toán'}

export default class Employee extends React.Component{
  
  columns = [
    {
      Header: () => (
        <span className='fa-task'>Ngày lập</span>
      ),
      accessor: 'createdAt',
      Cell: props => <div>{props.value}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Tên</span>
      ),
      accessor: 'user_id',
      Cell: props => <div>{props.value? props.value.name : ''}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Doanh Nghiệp</span>
      ),
      accessor: 'company_id',
      Cell: props => <div>{props.value? props.value.name : ''}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Phân quyền</span>
      ),
      accessor: 'type',
      Cell: props => <div>{type[props.value]}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Tổng thanh toán</span>
      ),
      // accessor: 'expire_at',
      Cell:<div>{0}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Tổng khuyến mãi</span>
      ),
      // accessor: 'value',
      Cell:<div>{0}</div>
    },
    {
      Header: () => (
        <span className='fa-task'>Tổng hoàn tiền</span>
      ),
      // accessor: 'refund',
      Cell:<div>{0}</div>
    }
  ] 

  
  constructor(props){
        super(props);
        this.state = {
            page : 0,
            pages: null,
            pageSize: 20,
            sort: '-createdAt',
            data: [],
            isLoading: true,
            users: [],
            user: {name:'Người dùng', _id: ''}
        }
    }

    
    getData = (info) =>{
      let { page, pageSize, sorted } = info;
      let { user } = this.state;
      let sort = sorted ? sorted.map(sort => `${sort.desc ? '-' : ''}${sort.id}`).toString() : this.state.sort;
      api.getEmployees({ limit: pageSize, page: page + 1, sort, user_id: user._id }).then(response => {
        if (response.success) {
          this.setState({ data: response.data, page, pageSize, sort, pages: response.totalPage });
        }
        else {
        }
      })
    }


    render(){
      let { page, pages, pageSize, sort, data, users, user } = this.state;
  
      return (
        <Page>
          <Row>
            <Col>
              <Card className='mb-3'>
                <CardHeader>
                  Danh sách Nhân viên
                </CardHeader>
                <CardBody>
                  <ReactTable
                    className='overflow-hidden'
                    data={data}
                    columns={this.columns}
                    // defaultPageSize={20}
                    defaultPageSize={pageSize}
                    page={page}
                    pages={pages}
                    pageSize={pageSize}
  
                    onFetchData={this.getData}
                    showPageJump={true}
                    manual
                  // loading={isLoading}
                  />
                </CardBody>
              </Card>
            </Col>
          </Row>
          <Toast ref={(ref) => this.toast = ref} />
        </Page>
      )
    }
}