import Page from '../../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import Api from '../../utils/api';
import { Link } from 'react-router-dom';
import { isEmpty } from 'lodash';
import EditAdmin from './EditAdmin';
import Select from 'react-select';
import DropDown from '../../components/DropDown';
import Toast from '../../components/Toast';
import Config from '../../utils/config';

export default class Admin extends React.Component {

columns = [
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Email
      </span>
    ),
    minWidth: 100,
    accessor: 'email',
    Cell: props => <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>{props.value}</div>
  },
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Tên
      </span>
    ),
    minWidth: 100,
    accessor: 'name',
    Cell: props => <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>{props.value}</div>
  },
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Tên đăng nhập
      </span>
    ),
    minWidth: 100,
    accessor: 'user_name',
    Cell: props => <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>{props.value}</div>
  },
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Trạng thái
      </span>
    ),
    minWidth: 60,
    accessor: 'status',
    Cell: props => {
      let text = 'Hoạt động';
      let color = 'text-success';
      if (props.value === 'disable') {
        text = 'Khóa';
        color = 'text-danger';
      }
      return (
        <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }} className={color}>
          {text}
        </div>
      );
    }
  },
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Nhóm quyền
      </span>
    ),
    minWidth: 60,
    accessor: 'permission_group_id',
    Cell: props => {
      let listGroupPermission = this.state.listGroupPermission;
      // if(listGroupPermission && listGroupPermission.length > 0){
      let index = 0;
      if(props.value){          
        for(let i = 0; i < listGroupPermission.length; i++){
          if(props.value._id.toString() === listGroupPermission[i]._id.toString()){
            index = i +1;
            break;
          }
        }
      } 
      listGroupPermission = [{_id:'none',name:'Không'}].concat(listGroupPermission);
        // return <span>{props.value? props.value.name:''}</span>
      
      return(
        <div style={{display:'flex', justifyContent:'center'}}>
          <DropDown
            variant="secondary"
            defaultSelected = {index}
            data={listGroupPermission}
            onChange = {(index, id)=> this.onChangeGroupPermission(index,id, props.original._id)}
          />
        </div>
      ) 
      
      // }
      // return <div>{"Chưa có nhóm quyền nào"}</div>
    }
  }
];

  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      pages: null,
      page: 0,
      pageSize: 10,
      permissions: [],
      permissionOptions: [],
      listGroupPermission:[]
    };
  }
  componentDidMount() {
    Api.getAllGroupPermission().then(response => {
      if (response.success) {;
        const permissionOptions = response.data.map(item => ({ value: item._id, label: item.name }));
        this.setState({ permissions: response.data, permissionOptions, listGroupPermission: response.data });
      }
      else{
        this.toast.showMessage('đã xảy ra lỗi lấy danh sách phân quyền');
      }
    });
    this.getData(this.state);
  }

  onChangeGroupPermission(index, permission_group_id, admin_id){
    let data = {
      admin_id,
      permission_group_id
    }
    if((permission_group_id === 'none')){
      Api.adminOutGroup(admin_id).then(response => {
        if(response.success){
          this.toast.showMessage('Thành công')
        }
        else {
          this.toast.showMessage('Đã xảy ra lỗi');
        }
      })
    }
    else{
      Api.addAdminToPermissionGroup(data).then(response => {
        if(response.success){
          this.toast.showMessage('Đổi nhóm thành công');
        }
        else {
          this.toast.showMessage('Đã xảy ra lỗi');
        }
      });
    }
  }

  getData = (state, instance) => {
    this.setState({ isLoading: true });
    Api.getAdmins(state.pageSize, state.page + 1).then(response => {
      if (response.success) {
        const data = response.data.map(item => {
          return item;
        });
        this.setState({
          isLoading: false,
          data,
          pages: response.totalPages
        });
      } else {
        this.setState({
          isLoading: false,
          error: 'Tải dữ liệu lỗi.'
        });
      }
    });
  };

  onReload = (page, pageSize) => {
    this.getData({ page, pageSize });
  };

  onFilter = value => {
  };

  render() {
    const { data, isLoading, pages, pageSize, permissions, permissionOptions } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'Danh sách Admin', active: true }]} className='TablePost'>
        <Row>
          {/* <Col className="col-md-2">
            <Button block='true' color="success">
              Thêm nhóm quyền
            </Button>
          </Col> */}
          <Col>
            <Card className='mb-3'>
              <CardHeader>
                Danh sách Admin
                <Row className='row justify-content-end'>
                  <Col className='md-6'>
                    <Select name='permissions' onChange={value => this.onFilter(value)} options={this.state.permissionOptions} />
                  </Col>
                  <Col className='md-3'>
                    <Link to={Config.HOST_PREFIX + '/create-admin'}>
                      <Button color='primary'>Thêm</Button>
                    </Link>
                  </Col>
                </Row>
              </CardHeader>
              <CardBody>
                {!isEmpty(this.state.permissions) && (
                  <ReactTable
                    data={data}
                    resolveData={data=>data.map(item=>{
                      item.permissionMix = {
                        permission: item.permission_group_id,
                        groups: this.state.permissionOptions,
                      }
                      return item;
                    })}
                    loading={isLoading}
                    // manual
                    columns={this.columns}
                    defaultPageSize={pageSize}
                    // filterable
                    // onFetchData={this.getData}
                    // SubComponent={row => {
                    //   const ele = row.original;
                    //   return <EditAdmin ele={ele} permissionOptions={permissionOptions} onReload={() => this.onReload(row.page, row.pageSize)} />;
                    // }}
                  />
                )}
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Toast ref={(ref)=>this.toast = ref}/>
      </Page>
    );
  }
}
