import React, { Component } from 'react';
import PropTypes from 'prop-types';
import api from '../../utils/api';
import ReactTable from 'react-table';
import Notification from '../../components/Toast';

export default class Store extends Component {
  static propTypes = {
    company_id: PropTypes.string.isRequired
  };
  state = { store: [] };
  getData = state => {
    api
      .getStore({ company_id: this.props.company_id, select: 'name address total_pay total_refund total_staff total_discount total_order status' })
      .then(res => {
        if (res.success)
          this.setState({
            store: res.data
          });
        else this.notificationSystem.showMessage('failed');
      })
      .catch(err => this.notificationSystem.showMessage('failed'));
  };
  columns = [
    {
      Header: 'Tên cửa hàng',
      accessor: 'name',
      Cell: ({ value }) => value
    },
    {
      Header: 'Địa chỉ',
      accessor: 'address',
      Cell: ({ value }) => value
    },
    {
      Header: 'Danh sách nhân viên',
      accessor: 'total_staff',
      Cell: ({ value }) => value
    },
    {
      Header: 'Tổng đơn hàng',
      accessor: 'total_order',
      Cell: ({ value }) => value
    },
    {
      Header: 'Tổng thanh toán',
      accessor: 'total_pay',
      Cell: ({ value }) => value
    },
    {
      Header: 'Tổng khuyến mãi',
      accessor: 'total_discount',
      Cell: ({ value }) => value
    },
    {
      Header: 'Tổng hoàn tiền',
      accessor: 'total_refund',
      Cell: ({ value }) => value
    },
    {
      Header: 'Trạng thái',
      accessor: 'status',
      Cell: ({ value }) => value
    }
  ];
  render() {
    const { store } = this.state;
    return (
      <>
        <Notification ref={notificationSystem => (this.notificationSystem = notificationSystem)} />
        <ReactTable data={store} columns={this.columns} onFetchData={this.getData} showPagination={false} pageSize={store.length} />
      </>
    );
  }
}
