const bem = bn.create('sidebar');

const oldState = {
  isOpenReward: false,
  isOpenUser: false,
  isOpenVideo: false,
  isOpenAdmin: false,
  isOpenCreateContent: false,
  isOpenCompany: false
};

const listMenu = { userSubmenu, videoSubmenu, adminSubmenu, companySubmenu };

const listSubContent = [
  { openName: 'User', renderName: 'Người dùng', selectedState: 'isOpenUser', selectedSubMenu: 'userSubmenu' },
  { openName: 'Company', renderName: 'Doanh nghiệp', selectedState: 'isOpenCompany', selectedSubMenu: 'companySubmenu' },
  { openName: 'Video', renderName: 'Video', selectedState: 'isOpenVideo', selectedSubMenu: 'videoSubmenu' },
  { openName: 'Admin', renderName: 'Admin', selectedState: 'isOpenAdmin', selectedSubMenu: 'adminSubmenu' },
]
class Sidebar extends React.Component {
  constructor(props) {
    super(props);
    let currentState = stateManager.getState();
    let submenu = currentState.submenu || '';
    let indexActiveSidebar = currentState.indexActiveSidebar;
    let firstHandel = currentState.firstHandel;
    this.state = {
      permissions: [],
      admin: currentState.admin,
      ...oldState,
      submenu,
      indexActiveSidebar,
      firstHandel,
      // submenuMod:
    }
  }

  componentDidMount() {
    let currentState = stateManager.getState();
    // this.setState({firstHandel: true});
    let permissions = [];
    if (currentState && currentState.permissionGroup) {
      permissions = currentState.permissionGroup.permission_ids;
      this.setState({ permissions});
    }
    else if (currentState.admin && currentState.admin.permission_group_id) {
      api.getPermissionGroupById(currentState.admin.permission_group_id._id).then(response => {
        if (response.success) {
          let permissionGroup = response.data;
          stateManager.notice({ permissionGroup });
          permissions = permissionGroup.permission_ids;
          this.setState({ permissions});
        }
        else {
          this.toast.showMessage('Đã xảy ra lỗi: phân quyền');
        }
      })
    }
  }

  handleClick = name => () => {
    // this.setState(prevState => {
    //   const isOpen = prevState[`isOpen${name}`];
    //   oldState[`isOpen${name}`] = !isOpen;
    //   return {
    //     [`isOpen${name}`]: !isOpen
    //   };
    // });
  };

  getSubContent(nameOpen, renderName, selectedState, selectedSubMenu) {
    return {
      nameOpen, renderName, selectedState, selectedSubMenu
    }
  }

  selectHandle(menu, index){
    let{submenu} = this.state;
    
    if(submenu !== menu){
      stateManager.notice({submenu:menu, indexActiveSidebar: index, firstHandel: true});
      this.setState({submenu: menu, indexActiveSidebar: index});
    }
  }

  selectHandleMod(submenu, index){
    stateManager.notice({submenu, firstHandel: true, indexActiveSidebar: index});
    this.setState({submenu, indexActiveSidebar: index});
  }

  selectSubMenu(){
    stateManager.notice({firstHandel: false});
  }

  render() {
    let {submenu, indexActiveSidebar, firstHandel, admin} = this.state;
    let renderList = [], submenuRender = [], redirect = [];
    if(firstHandel) {
      let url = admin.name === 'admin' ? listMenu[submenu][0].to : submenu[0].to;
      redirect.push(<Redirect to={Config.HOST_PREFIX + url} />);
    }

    if(submenu !== '' && submenu){
      let listSubmenus = admin.name === 'admin' ? listMenu[submenu] : submenu;
      listSubmenus.forEach((sub,index)=>{
        submenuRender.push(
          <NavItem key={index} className={bem.e('nav-item')}>
            <BSNavLink to={sub.to} onClick={()=>this.selectSubMenu()} tag={NavLink}  exact={sub.exact}>
              <div className='d-flex'>
              <MdDashboard className={bem.e('nav-item-icon')} />
              <span className=''>{sub.name}</span>
              </div>
            </BSNavLink>
          </NavItem>
        )
      })
    }

    let permissions = this.state.permissions;
    if (this.state.admin.user_name === 'admin') {
      listSubContent.forEach((content, index)=> {
        let active = index === indexActiveSidebar ? 'active' : '';
        renderList.push(
            <NavItem key={index} className={bem.e('nav-item')} >
              <BSNavLink to="#" onClick={()=>this.selectHandle(content.selectedSubMenu, index)} 
              tag={NavLink} className={active} exact={false}>
                <div className='d-flex'>
                  <MdSend className={bem.e('nav-item-icon')} />
                  <span className=''>{content.openName}</span>
                </div>
              </BSNavLink>
            </NavItem>
        )
      });
    }
    else if (permissions && permissions.length > 0) {
      let usersSide = [], companySide = [], videoSide = [], adminSide = [];

      for (let i = 0; i < permissions.length; i++) {
        let path = permissions[i].path.toString();
        if (listPathPermission.user.indexOf(path) > -1) {
          usersSide.push(listPath[path]);
        }
        else if (listPathPermission.admin.indexOf(path) > -1) {
          adminSide.push(listPath[path]);
        }
        else if (listPathPermission.company.indexOf(path)> -1) {
          companySide.push(listPath[path]);
        }
        else if (listPathPermission.video.indexOf(path) > -1) {
          videoSide.push(listPath[path]);
        }
      }
      let SideContent = [
        { openName: 'User', renderName: 'Người dùng', selectedState: 'isOpenUser', submenu: usersSide },
        { openName: 'Companyy', renderName: 'Doanh Nghiệp', selectedState: 'isOpenPromotion', submenu: companySide },
        { openName: 'Video', renderName: 'Video', selectedState: 'isOpenVideo', submenu: videoSide },
        { openName: 'Admin', renderName: 'Quản trị', selectedState: 'isOpenAdmin', submenu: adminSide },
      ]



      SideContent.forEach((content, index) => {
        if (content.submenu.length > 0) {
          let active = index === indexActiveSidebar ? 'active' : '';
          renderList.push(<div>
            <NavItem key={index} className={bem.e('nav-item')}>
              <BSNavLink to="#" onClick={()=> this.selectHandleMod(content.submenu, index)}
              tag={NavLink} className={active} exact={false}>
                <div className='d-flex'>
                  <MdPages className={bem.e('nav-item-icon')} />
                  <span className=''>{content.renderName}</span>
                </div>
              </BSNavLink>
            </NavItem>
            
          </div>
          );
        }
      });
    }


    return (<aside className={bem.b()} data-image={sidebarBgImage}>
        {redirect}
        <div className={bem.e('background')} style={sidebarBackground} />
        <div className={bem.e('content')}>
          <Navbar>
            <a className='navbar-brand d-flex' href='/admin'>
              <img src={logo200Image} width='40' height='30' className='pr-2' alt='' />
              <span className='text-white'>My Vietnamnet</span>
            </a>
          </Navbar>
          <Nav style={{height:'65%'}} vertical>
            {submenuRender}
          </Nav>
          <Nav vertical>
            {renderList}
          </Nav>
        </div>
        
        <Toast ref={(ref) => this.toast = ref} />
      </aside>
    );
  }
}

export default Sidebar;





