import Page from '../components/Page';
import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button, Input } from 'reactstrap';
import ReactTable from 'react-table';
import 'react-table/react-table.css';
import Api from '../utils/api';
import ImageAsync from '../components/ImageAsync';
import ApproveKYC from './ApproveKYC';
import Moment from '../components/Moment';
import { Link } from 'react-router-dom';
import Config from '../utils/config';

const getPassport = (passportImages = []) => {
  let images = {};
  passportImages.map(image => (images = { ...images, [image.name]: { path: image.path, status: image.status } }));
  return images;
};

const columns = [
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Email
      </span>
    ),
    minWidth: 100,
    accessor: d => [d.email, d._id],
    id: 'email',
    Cell: ({ value }) => {
      return (
        <Link to={`${Config.HOST_PREFIX}/user-detail?id=${value[1]}`}>
          <div style={{ wordWrap: 'break-word', whiteSpace: 'normal' }}>
            <b>{value[0]}</b>
          </div>
        </Link>
      );
    }
  },
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Selfy
      </span>
    ),
    accessor: 'selfy_image',
    minWidth: 100,
    Cell: ({ value }) => {
      if (value && value.path) {
        return <ImageAsync path={value.path} />;
      } else return <div>none</div>;
    }
  },
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Front
      </span>
    ),
    minWidth: 100,
    accessor: 'passport_image',
    Cell: ({ value }) => {
      if (value) {
        const passportImages = getPassport(value);
        if (passportImages.backside) return <ImageAsync path={passportImages.front.path} />;
        return <div>none</div>;
      } else return <div>none</div>;
    }
  },
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Back
      </span>
    ),
    minWidth: 100,
    accessor: 'passport_image',
    row: {},
    Cell: ({ value }) => {
      if (value) {
        const passportImages = getPassport(value);
        if (passportImages.backside) return <ImageAsync path={passportImages.backside.path} />;
        else return <div>none</div>;
      } else return <div>none</div>;
    }
  },
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Cập nhật lần cuối
      </span>
    ),
    accessor: 'updatedAt',
    Cell: ({ value }) => {
      if (value) {
        return (
          <div>
            <Moment format='DD/MM/YYYY, h:mm:ss A' date={value} />
          </div>
        );
      }
    }
  },
  {
    Header: () => (
      <span>
        <i className='fa-tasks' /> Trạng thái
      </span>
    ),
    accessor: 'status',
    Cell: ({ value }) => {
      let text = 'Không KYC';
      let color = 'text-muted';
      if (value === 'approve-kyc') {
        text = 'Đã xác thực';
        color = 'text-success';
      } else if (value === 'reject-kyc') {
        text = 'Đã từ chối';
        color = 'text-danger';
      } else if (value === 'pending-kyc') {
        text = 'Đang chờ';
        color = 'text-warning';
      }
      return <span className={color}>{text}</span>;
    }
  }
];
class Table_KYC extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [],
      pages: null,
      page: 0,
      pageSize: 10,
      status: 'pending-kyc'
    };
  }
  onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
    this.getData({ ...this.state, status: e.target.value });
  };
  onReload = () => {
    this.getData(this.state);
  };

  getData = (state, instance) => {
    this.setState({ loading: true, pageSize: state.pageSize });
    Api.getUserByStatus({ status: state.status || this.state.status, limit: state.pageSize, page: state.page + 1 }).then(response => {
      if (response.success) {
        const data = response.data;
        this.setState({
          isLoading: false,
          data,
          pages: response.totalPages
        });
      } else {
        this.setState({
          isLoading: false,
          error: 'Tải dữ liệu lỗi.'
        });
      }
    });
  };

  render() {
    const { data, isLoading, pages, pageSize, status } = this.state;
    return (
      <Page breadcrumbs={[{ name: 'KYC', active: true }]} className='TablePost'>
        <Row>
          <Col>
            <Card className='mb-3'>
              <CardHeader>
                Danh sách KYC
                <Input className=' ml-5 d-inline' type='select' name='status' value={status} style={{ width: 200 }} onChange={this.onChange}>
                  <option value='pending-kyc'>Pending</option>
                  <option value='approve-kyc'>Approved</option>
                  <option value='reject-kyc'>Rejected</option>
                </Input>
                <Button color='primary' className='float-right' onClick={this.onReload}>
                  refresh
                </Button>
              </CardHeader>
              <CardBody>
                <ReactTable
                  data={data}
                  loading={isLoading}
                  manual
                  sortable={false}
                  columns={columns}
                  defaultPageSize={pageSize}
                  onFetchData={this.getData}
                  resizable={false}
                  pages={pages}
                  SubComponent={row => {
                    const passportImages = getPassport(row.original.passport_image);
                    return (
                      <ApproveKYC
                        id={row.original._id}
                        selfy_status={row.original.selfy_image && row.original.selfy_image.status}
                        front_passport_status={passportImages.front && passportImages.front.status}
                        backside_passport_status={passportImages.backside && passportImages.backside.status}
                      />
                    );
                  }}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Page>
    );
  }
}

export default Table_KYC;
