import Config from './config';
import mergeObject from './merge-object';

class ApiUser {
    
    constructor(host) {
        this.host = host;
    }

    setToken(token) {
        this.token = token;
    }

    async fetchData(api, method = 'GET', body) {
        try {
            const options = {
            method,
            headers: {
                Accept: 'application/json'
            },
            body
            };
            if (typeof body === 'string') {
            options.headers['Content-Type'] = 'application/json';
            }
            if (this.token) {
            options.headers['Authorization'] = 'Bearer ' + this.token;
            }

            const response = await fetch(api, options);
            const json = await response.json();
            return { ...json, code: json.statusCode, success: response.ok };
        } catch (err) {
            return { code: -1, errors: err, success: false };
        }
    }

    getQuery(query) {
        let fullQuery = mergeObject({}, query);
        let api = '?';
        for (let key in fullQuery) {
          api += `${key}=${fullQuery[key]}&`;
        }
        return api;
    }

    login(data){
        const api = this.host + '/api/auth/user-login';
        return this.fetchData(api, 'POST', JSON.stringify(data));
    }
    createVideo(data){
        const api = this.host + '/api/v_/video/user';
        return this.fetchData(api,'POST', JSON.stringify(data));
    }
    getPersonalVideos({limit=0, page=0, sort=''}){
        if(sort == '') sort = '-createdAt';
        const api = this.host + `/api/v_/video/user?sort=${sort}&limit=${limit}&page=${page}`;
        return this.fetchData(api);
    }
    verifyUser(type, token, email){
        let data = {
            type,
            token,
            email
        }
        const api = this.host + `/api/auth/verify`
        return this.fetchData(api, 'PUT', JSON.stringify(data));
    }

    getVideosYoutube({url, url_type, regionCode, maxResults, videoCategoryId, search_key, videoDuration}){
        const api = this.host + `/api/v_/video/user/youtube` + this.getQuery({url, url_type, regionCode, maxResults, videoCategoryId, search_key, videoCategoryId, videoDuration});
        return this.fetchData(api);
    }

    postManyVideo(data){
        const api = this.host + '/api/v_/video/user/youtube';
        return this.fetchData(api,'POST',JSON.stringify(data));
    }
}
const apiUser = new ApiUser(Config.BACKEND_HOST);
export default apiUser; 